import Vue from "vue";
import { createRouter, createWebHashHistory } from "vue-router";

import HomePage from "@/components/Home.vue";
import Convoy_Seven from "@/components/Convoy_Seven.vue";
import Bumblebee from "@/components/Bumblebee.vue";
import SteelCable from "@/components/SteelCable.vue";
import Bluetooth from "@/components/Bluetooth.vue";
import BluetoothBumblebee from "@/components/BluetoothBumblebee.vue";
import BluetoothSteelCable from "@/components/BluetoothSteelCable.vue";
import BluetoothBazz from "@/components/BluetoothBazz.vue";
import TestFactory from "@/components/TestFactory.vue";
const router = createRouter({
  mode: "hash",
  history: createWebHashHistory(),
  routes: [
    {
      path: "/",
      name: "HomePage",
      component: HomePage,
    },
    {
      path: "/Convoy_Seven",
      name: "Convoy_Seven",
      component: Convoy_Seven,
    },
    {
      path: "/Bumblebee",
      name: "Bumblebee",
      component: Bumblebee,
    },
    {
      path: "/SteelCable",
      name: "SteelCable",
      component: SteelCable,
    },
    {
      path: "/Bluetooth",
      name: "Bluetooth",
      component: Bluetooth,
    },
    {
      path: "/BluetoothBumblebee",
      name: "BluetoothBumblebee",
      component: BluetoothBumblebee,
    },
    {
      path: "/BluetoothSteelCable",
      name: "BluetoothSteelCable",
      component: BluetoothSteelCable,
    },
    {
      path: "/BluetoothBazz",
      name: "BluetoothBazz",
      component: BluetoothBazz,
    },
    {
      path: "/TestFactory",
      name: "TestFactory",
      component: TestFactory,
    },
  ],
});

export default router;
