<!--变七机器人通讯结构-->
<template>
    <h3 >变七机器人通讯结构</h3>
    <p>连接方式         : 串口</p>
    <p>配置参数baudRate : 115200</p>
    <p>配置参数parity   : 0</p>
    <p>配置参数dataBits : 8</p>
    <p>配置参数stopBits : 1</p>
    <el-button type="primary" v-on:click="requestSerialPort()">连接</el-button> 
     <label> 连接成功后，发送[0xFF, 0xFF, 0x02, 0xF5, 0xF7] 指令0xF5是进入PC编程模式 0xF7是(指令+数据)和%256的结果
         <br> 发送后接收数据直到接收[0xFF,0xFF,0x04	0xF4,0x4F 0x4B,0x93] 当中的数据需要进行处理</label>
    <p></p>
    <el-button type="primary" v-on:click="closePose()">关闭</el-button><label>关闭需要发送指令 [0xFF, 0xFF, 0x02, 0xF6, 0xF8] 然后断开端口</label>
    <p></p>
    <el-button type="primary" v-on:click="timeSendingPosition()">时时发送位置</el-button> 
    <label>例如:左肩旋转: </label>
    <el-input v-model="ArmInput" style="width: 50px;"/><label>控制舵机移动[0xFF,0xFF,数据长度,指令(0x01),数据(位置+速度),sum和]指定左肩的下标控制</label>
    <p></p>
    <el-button type="primary" v-on:click="humanoidInitial()" >人形初始位置保存</el-button>
    <label>初始位置在人形的时候就已经读取过，具体是那个看代码，保存指令为[0xFF,0xFF,数据长度,指令(0x03) 数据,sum和] 更改方式也是指定的某个轴的下标</label>
    <p></p>
    <el-button type="primary" v-on:click="squatStart()">下蹲初始位置</el-button>
    <label>初始位置在人形的时候就已经读取过，具体是那个看代码，保存指令为[0xFF,0xFF,数据长度,指令(0x0F) 数据,sum和] 更改方式也是指定的某个轴的下标</label>
    <p></p>
    <input type="file" @change="actionDownload" /> 下载功能
    <p></p>
    <label>下载是将pc上的数据载入到机器人当中，这里用一个.shr机器人保存再本地的文件开始,下载的指令顺序是:删除(0x15),创建(0x14),修改(0x17),下载(0x02)</label>
    <p></p>
    <label>这个地方有个bug就是文件名是数字且这个数字小于10的情况下发送到机器人的数据会多一个具体原因不明(感觉是机器人内部处理的问题，用纯PC的软件不会)</label>
    <p></p>
    <h4>上传功能</h4>
    <label>上传功能首先需要读取一下机器人内部的上传动作文件名</label>
    <p></p>
    <el-button type="primary" v-on:click="getActionName()">读取</el-button>
     <el-select v-model="selectedOption">
        <el-option
          v-for="option in vOpentions"
          :key="option"
          :label="option"
          :value="option"
        >
        </el-option>
     </el-select> 
    <label>以绝对路径读取文件夹指令为[0xFF,0xFF,数据长度,指令(0x1A) 数据,sum和]</label>
    <p></p>
    <el-button type="primary" v-on:click="getAction()"> 上传</el-button>
    <label>上传，先选择要上传的文件，然后发送指令[0xFF,0xFF,数据长度,指令(0x16),0x3B,(固定数据) 数据(文件名称数据),sum和]</label>



</template>
<script>
import    { CreatePosData } from "/src/PosData.js";
import iconv from 'iconv-lite';

  export default{


    data(){
      return{
        port:{},
        isLink:false,
        initData:[],//存放4个基础位置
        ArmInput:0,
        dataArr: [],
        selectedOption:"",
        vOpentions:[]
      }
    },
    methods:{
        async requestSerialPort()//连接
        {
          try
          {
            const port = await navigator.serial.requestPort();
            if(port!=null)
            {
                await  port.open({baudRate:115200,
                  parity:"none",
                  dataBits:8,
                  stopBits:1,
                  bufferSize: 4096,
                })
                this.port = port;
                //console.log(port);
                //console.log(this.port);
                const data = new Uint8Array([0xFF,0xFF,0X02,0xF5,0xF7]); //发送的指令，这里是简写，最好是自己计算下sum和
                const writer = port.writable.getWriter();
                // console.log(writer)
                await writer.write(data);
                await writer.releaseLock();
                const reader  = await port.readable.getReader(); 
                this.isLink = false;       
                var dataValue =[];    
                while(true)
                {
                  if(this.isLink==true)
                    {
                      // //4个基础位置,有时候会少一个，不知道是什么原因，这个版本中一定要获取到所有初始数据，那怕不用
                      // console.log(this.initData[0]);//初始位置
                      // console.log(this.initData[1]);
                      // console.log(this.initData[2]);
                      // console.log(this.initData[3]);
                      reader.releaseLock();
                      break;
                    }
                    const{value, done} = await  reader.read();
                    if(done)
                    {
                        reader.releaseLock()
                        break;
                    }
                   
                 
                  // console.log(value)
                  // dataValue.concat(value)
                  // dataValue= dataValue.concat(value)
               
                  for(let i =0 ;i<value.length;i++)
                  {
                    dataValue[dataValue.length] =value[i]
                  }
                  // console.log(dataValue)
                  // console.log(dataValue.length)
                  for(let i=0;i<dataValue.length;i++)
                  {
                   
                    //帧头和指令的判断
                    if(dataValue[i]==0xFF && dataValue[i+1]==0xFF)
                    {
                      var len   = dataValue[i + 2];//长度 
                      var order = dataValue[i + 3];//指令
                     
                      if (len == 0x04 && order == 245 && dataValue[i + 4] == 0x4f && dataValue[i + 5] == 0x4B)
                      {
                        if (dataValue[i + 6] == (len + order + 0x4f + 0x4B) % 256)
                        {
                        
                          this.isLink=true;
                          break;
                        }
                      }
                    }
                  }
                }
                let flag=0;
                for(let i=0;i<dataValue.length;i++)
                {
                  if(dataValue[i]==0xFF && dataValue[i+1]==0xFF)
                  {
                    var len   = dataValue[i + 2];//长度 
                    var order = dataValue[i + 3];//指令
                    if(len == 50 && order ==245)
                      {
                        var dataArray = [];
                        for(let j =0 ;j<48;j++)
                        {
                          dataArray[j]= dataValue[i+j+4];
                        }
                        i+=48 + 1 + 3;
                        this.initData[flag]=dataArray;
                        flag++;
                      }
                  }
                }
                 //console.log(this.initData[0]);//初始位置
                 //console.log(this.initData[1]);
                 //console.log(this.initData[2]);
                 // console.log(this.initData[3]);

            }
          }
          catch(err){
             console.error(err);
          }
        },
        async closePose()//关闭
        {
            try{
              //console.log(this.port)
              const data = new Uint8Array([0xFF, 0xFF, 0x02, 0xF6, 0xF8]); //发送的指令，这里是简写，最好是自己计算下sum和
              const writer = this.port.writable.getWriter();
              await writer.write(data);
              //发送发送指令后机器人会主动关闭
              // await writer.close();
              // await this.port.close();
              this.isLink=false;

            }catch(err){
              console.error(err);
            }
        },
        async timeSendingPosition(){//实时位置         
          try{
                if( this.isLink==true)
                {
                  var data= this.initData[0];
                  // console.log(data)
                  data[12]+= parseInt( this.ArmInput);
                  data[data.length]=0x19;
                  var sendData =[];
                  sendData[0] = 0xFF;
                  sendData[1] = 0xFF;
                  sendData[2] = 0x33;//长度
                  sendData[3] = 0x01;//指令
                  var sum= 0x33+0x01;
                  for(let i =0;i<data.length;i++)
                  {
                    sendData[4+i] = data[i];
                    sum+=data[i];
                  }
                 sum = sum%256;
                 
                sendData[sendData.length]=sum;
                // console.log(sendData)
                 var dataUt = new Uint8Array(sendData);
                 const writer = this.port.writable.getWriter();
                await writer.write(dataUt);
                await writer.releaseLock();
                }
          }catch(err)
          {
            console.error(err);
          }
        },
        async humanoidInitial()//人形初始位置
        {
            try
            {
              if(this.isLink==true)
              {
                var data= this.initData[0];//可更改
              
                var sendData =[];
                sendData[0] = 0xFF;
                sendData[1] = 0xFF;
                sendData[2] = 0x32;//长度
                sendData[3] = 0x03;//指令
                var sum = 0x32+0x03;
                for(let i =0;i<data.length;i++)
                {
                    sendData[4+i] = data[i];
                    sum+=data[i];
                }
                 sum = sum%256;
                 sendData[sendData.length]=sum;
                 console.log(sendData)
                 var dataUt = new Uint8Array(sendData);
                 const writer = this.port.writable.getWriter();
                 await writer.write(dataUt);
                 await writer.releaseLock();
              }
            }
            catch(err)
            {
              console.error(err);
            }
          },
        async squatStart() //下蹲初始
        {
          try
            {
              if(this.isLink==true)
              {
                var data= this.initData[2];//可更改
                var sendData =[];
                sendData[0] = 0xFF;
                sendData[1] = 0xFF;
                sendData[2] = 0x32;//长度
                sendData[3] = 0x0F;//指令
                var sum = 0x32+0x0F;
                for(let i =0;i<data.length;i++)
                {
                    sendData[4+i] = data[i];
                    sum+=data[i];
                }
                 sum = sum%256;
                 sendData[sendData.length]=sum;
                 console.log(sendData)
                 var dataUt = new Uint8Array(sendData);
                 const writer = this.port.writable.getWriter();
                 await writer.write(dataUt);
                 await writer.releaseLock();
                 
              }
            }
            catch(err)
            {
              console.error(err);
            }

        },
        async actionDownload(event)//动作下载
        {
            //该处有问题，动作文件名是数字且小于10的情况下动作文件会多一个，以测发送的数据集合正常
            //该处为处理文件流的方式下载数据，如果是采用编译器的方式，数据处理流和这个类似
            try{
              const file = event.target.files[0]
              const reader = new FileReader()
              reader.readAsText(file, "UTF-8")
              //reader.readAsText(file, "gb2312")
              reader.onload = async (evt) =>
               {
              const fileString = evt.target.result
              const count = fileString.trim().split('\n').length
            
              this.dataArr = []
              for (let index = 0; index < count; index++)
              {
                const fileline = fileString.split("\r\n")[index].split(",")[0]
                // console.log(fileline)
                //const filelineNumber = (fileline.split(/\s+/)).map(Number)
                const filelineNumber = (fileline.split(/\s+/)).map(match => isNaN(match) ? match : match==""?" ": Number(match));
                this.dataArr.push(filelineNumber)
                //this.dataArr.push(fileline)
              }
              //这种"253  253  254  "是不需要处理的行
              //var sss ="253  253  254  "
              //console.log(this.dataArr)
              //console.log(isNaN('')?"1":"2")
             
               if(this.isLink==true)
              {
                  var nameArr = [];
                  var dataAction=[]
                  var dataArr =this.dataArr;
                  var len = dataArr.length;
                  for(let i=0;i<len;i++)
                  {
                    var _is_Action = false; //用于判断当前行是否为动作行
                     if(dataArr[i][0]==' ') //判断当前行为动作数据行
                     {
                      // console.log(i);
                        _is_Action=true;
                     }
                     if(dataArr[i][0] == 253 && dataArr[i][1]==253 && dataArr[i][2] == 254 && dataArr[i][3]!=" ")//名称
                     {
                       nameArr.push(dataArr[i][3]);
                     }
                     if(_is_Action==true)//最后得处理成那个文件名下的那个动作
                     {
                       dataAction.push(dataArr[i])
                     }
                 }
                 //先转化成PosData这个数据是pc用来在界面上展示动作按钮的数据,如果web编译器需要这个功能处理.shr文件的方式按照这个来
                 var last_loop_byte = 0;
                 len = dataAction.length;
                 var posDataArr=[];
                 for(let i=0; i<len; i++)
                 {
                    //方向处理
                    var _n1 = dataAction[i][5];
                    var _n2 = dataAction[i][6];
                    var _n3 = dataAction[i][7];
                    var _n4 = dataAction[i][8];
                    var _n5 = dataAction[i][9];
                    var _n6 = dataAction[i][10];
                    for(let j=0; j<8; j++)
                    {
                      let _Zero = _n1 & Math.pow(2, 7 - j);
                      if(_Zero!= 0)
                      {
                        dataAction[i][j+11] *= -1;
                      }
                    }
                    for(let j=0; j<8; j++)
                    {
                      let _Zero = _n2 & Math.pow(2, 7 - j);
                      if(_Zero!= 0)
                      {
                        dataAction[i][j+19] *= -1;
                      }
                    }
                    for(let j=0; j<8; j++)
                    {
                      let _Zero = _n3 & Math.pow(2, 7 - j);
                      if(_Zero!= 0)
                      {
                        dataAction[i][j+27] *= -1;
                      }
                    }
                    for(let j=0; j<8; j++)
                    {
                      let _Zero = _n4 & Math.pow(2, 7 - j);
                      if(_Zero!= 0)
                      {
                        dataAction[i][j+35] *= -1;
                      }
                    }
                    for(let j=0; j<8; j++)
                    {
                      let _Zero = _n5 & Math.pow(2, 7 - j);
                      if(_Zero!= 0)
                      {
                        dataAction[i][j+43] *= -1;
                      }
                    }
                    for(let j=0; j<8; j++)
                    {
                      let _Zero = _n6 & Math.pow(2, 7 - j);
                      if(_Zero!= 0)
                      {
                        dataAction[i][j+51] *= -1;
                      }
                    }
                    var Loop_Time=0;
                    var Is_Loop_Start=false;
                    var Is_Loop_End=false;
                    //用于判断是不是循环
                    if(dataAction[i][4]!=0 &&dataAction[i][4]>128)
                    {
                      Loop_Time = dataAction[i][4]-128;
                    }
                    else if(dataAction[i][4]!=0 &&dataAction[i][4]<128)
                    {
                      Loop_Time = dataAction[i][4];
                      Is_Loop_Start=false;
                      Is_Loop_End = true;
                    }
                    else if(dataAction[i][4]==0)
                    {
                     Is_Loop_Start=false;
                     Is_Loop_End=false;
                    }
                    var Pos = [];
                    var number = dataAction[i][3];
                    for(let j=0; j<48 ;j++)
                    {
                       Pos[j] = dataAction[i][j+11]
                    }
                    var Speed  =  dataAction[i][59];//速度
                    var Delay  =  dataAction[i][60];//延迟
                    var Music_State = 0;//音乐
                    let Music_Number= -1;
                    let Music_Chosen= -1;
                    if((dataAction[i][61]!=251 && dataAction[i][62]!=251 ) &&(dataAction[i][61]!=252 && dataAction[i][62]!=252 ))
                    {
                        Music_Chosen = dataAction[i][61]*256+dataAction[i][62];
                        Music_State = 2;
                    }
                    else if(dataAction[i][61] == 251 && dataAction[i][62] ==251)
                    {
                        Music_State = 4;
                    }
                    else if(dataAction[i][61] == 252 && dataAction[i][62] ==252)
                    {
                      Music_State = 3;
                    }
                    Music_Number=Music_Chosen;
                    if(dataAction[i][4]!=0)
                    {
                        if(last_loop_byte == 0 )
                        {
                          Is_Loop_Start= true;
                        }
                        else
                        {
                          Is_Loop_Start=false;
                        }
                        if(dataAction[i][4]<128)
                        {
                          Is_Loop_End=true;
                          Loop_Time= dataAction[i][4];
                        }
                        else
                        {
                          Is_Loop_End=false;
                          Loop_Time= dataAction[i][4]-128;
                        }
                    }
                    if(dataAction[i][4]>128)
                    {
                      last_loop_byte=dataAction[i][4];
                    }
                    else
                    {
                      last_loop_byte=0;
                    }                 
                    var posData={
                      number:number,
                      Name:1,
                      Pos:Pos,
                      Speed:Speed,
                      Delay:Delay,
                      Loop_Time:Loop_Time,
                      Is_Loop_Start:Is_Loop_Start,
                      Is_Loop_End:Is_Loop_End,
                      Music_State:Music_State,
                      Music_Number:Music_Number

                    }
                    //console.log(posData)
                    posDataArr.push(CreatePosData(posData));     
                 }
                
                 //再把PosData数据转化为下载的数据发送到机器人当中
                 len = posDataArr.length;
                 let currentNumber = 0;
                 var Loop_Time=-1;
                 var Is_In_Loop =false;
                 var currentData=[[]]

                 var flagNumber= posDataArr[0].number;
              
                 currentData[0]=[]
                 for(let i=0 ; i<len ;i++)
                 {
                  //  if(len == 1)
                  //   {
                  //     currentData[0]=[]
                  //     currentNumber=posDataArr[i].number
                  //   }
                  //   else
                  //   {
                  //     if(currentNumber!=posDataArr[i].number)
                  //     {
                  //       currentNumber =currentNumber+1;
                  //       //console.log(currentNumber)
                  //       currentData[currentNumber] =[]
                  //    }
                  //   } 
                    if(flagNumber!=posDataArr[i].number)
                    {
                      flagNumber= posDataArr[i].number;
                      currentNumber++;
                      currentData[currentNumber]=[]
                    }
                 
                    var currentPos = [];
                    currentPos.push(0xFF);
                    currentPos.push(0xFF);
                    currentPos.push(0x3D);
                    currentPos.push(0x02);//指令

                    //处理循环
                    if(!Is_In_Loop)
                    {
                      currentPos[4] =0
                    }
                    
                    if(Is_In_Loop && !posDataArr[i].Is_Loop_Start && !posDataArr[i].Is_Loop_End)
                    {
                      currentPos[4] = Loop_Time +128;
                    }
                    if(posDataArr[i].Is_Loop_End)
                    {
                      Is_In_Loop=false;
                      currentPos[4] = Loop_Time;
                      Loop_Time=0;
                    }
                    if(posDataArr[i].Is_Loop_Start)
                    {
                      Is_In_Loop=true;
                      Loop_Time = posDataArr[i].Loop_Time;
                      currentPos[4] = Loop_Time+128;
                    }
                    
                    var _int_temp1 = 0;
                    var _int_temp2 = 0;
                    var _int_temp3 = 0;
                    var _int_temp4 = 0;
                    var _int_temp5 = 0;
                    var _int_temp6 = 0;
                  
                    for(let k = 0 ; k< 48 ;k++)
                    {
                   
                        if(k < 8)
                        {
                            if(posDataArr[i].Pos[k]<0)
                            {
                              _int_temp1 += parseInt(Math.pow(2,7-k));
                              posDataArr[i].Pos[k]= posDataArr[i].Pos[k] * -1;
                            }
                        }
                       
                        if(k>= 8 && k<16)
                        {
                          if(posDataArr[i].Pos[k]<0)
                          {
                            _int_temp2+= parseInt(Math.pow(2,15-k));
                            posDataArr[i].Pos[k]= posDataArr[i].Pos[k] * -1;
                          }
                        }
                     
                        if(k>=16 && k<24)
                        {
                          if(posDataArr[i].Pos[k]<0)
                          {
                            _int_temp3+= parseInt(Math.pow(2,23-k));
                            posDataArr[i].Pos[k]= posDataArr[i].Pos[k] * -1;
                          }
                        }
                     
                        if(k>=24 && k<32)
                        {
                          if(posDataArr[i].Pos[k]<0)
                          {
                            _int_temp4+= parseInt(Math.pow(2,31-k));
                            posDataArr[i].Pos[k]= posDataArr[i].Pos[k] * -1;
                          }
                        }
                      
                        if(k>=32 && k<40)
                        {
                          if(posDataArr[i].Pos[k]<0)
                          {
                            _int_temp5+= parseInt(Math.pow(2,39-k));
                            posDataArr[i].Pos[k]= posDataArr[i].Pos[k] * -1;
                          }
                        }
                     
                        if(k>=40 && k<48)
                        {
                          if(posDataArr[i].Pos[k]<0)
                          {
                            _int_temp6+= parseInt(Math.pow(2,47-k));
                            posDataArr[i].Pos[k]= posDataArr[i].Pos[k] * -1;
                          }
                        }
                    }
               
                    currentPos[5] = _int_temp1;
                    currentPos[6] = _int_temp2;
                    currentPos[7] = _int_temp3;
                    currentPos[8] = _int_temp4;
                    currentPos[9] = _int_temp5;
                    currentPos[10] = _int_temp6;
                    for(let k=0; k<48;k++)
                    {
                      currentPos[k+11]= posDataArr[i].Pos[k]
                    }
                    
                   
                    currentPos[59] = posDataArr[i].Speed;
                    currentPos[60] = posDataArr[i].Delay;
                    switch(posDataArr[i].Music_State)
                    {
                        case 1:
                          currentPos[61] = parseInt(posDataArr[i].Music_Number / 256);
                          currentPos[62] = parseInt(posDataArr[i].Music_Number % 256);
                        break;
                        case 2:
                          currentPos[61] = parseInt(posDataArr[i].Music_Number / 256);
                          currentPos[62] = parseInt(posDataArr[i].Music_Number % 256);
                        break;
                        case 3:
                          currentPos[61] = 252;
                          currentPos[62] = 252;
                        break;
                        case 4:
                          currentPos[61] = 251;
                          currentPos[62] = 251;
                        break;
                    }
                    var sum =0;
                    //计算sum和
                  
                    for(let k =2; k<63;k++)
                    {
                      sum+=  currentPos[k];
                    }
                    sum = sum%256;
                    currentPos[63]= sum;
                   
                    currentData[currentNumber].push(currentPos)
                 }
                console.log(currentData)
                 //向机器人发送指令上传文件
                len = nameArr.length 
               
               for(let i=0;i<len; i++)
                //for(let i=0;i<1; i++)
                {
                
                    //每次循环需要延迟，因为机器人不可能接收的那么快
                    let promise = new Promise((resolve, reject) => {
                                   setTimeout(() => resolve(), 50)
                                    });
                     await  promise;
                     var selectedName = nameArr[i];
                     console.log(selectedName);
                     var typeName = selectedName.substring(0,4);//用于判断文件夹名
                     var actionName = selectedName.substring(4);//文件名
                     if(typeName == "地面::")
                     {
                        typeName = "GroundAction/";
                     }
                     else if(typeName == "凌空::")
                     {
