        async getActionName()//获取动作文件名
        {
            try{
              if(this.isLink==true)
              {
                 var nameByte = Array.from(iconv.encode("GroundAction",'gb2312'));
                 var length = nameByte.length;
                 var sendLength  = length+2;
                 var sendByte = [];
                 sendByte[0] = 0xFF;
                 sendByte[1] = 0xFF;
                 sendByte[2] = sendLength;
                 sendByte[3] = 0x1A;
                 var sum = sendLength + 0x1A;
                 for(let i =  0; i< length; i++)
                 {
                   sendByte.push(nameByte[i]);
                   sum+=nameByte[i];
                 }
                 sum = sum%256;
                 sendByte.push(sum);
                 var dataUt = new Uint8Array(sendByte);
                 const write = this.port.writable.getWriter();
                 await write.write(dataUt);
                 const reader = await this.port.readable.getReader();
                 var dataValue=[];
                 var redResult =false;
                 while(!redResult)
                 {
                    const{value,done} = await reader.read();
                    if(done)
                    {
                        reader.releaseLock();
                        break;
                    }
                    for(let i=0;i<value.length;i++)
                    {
                      dataValue[dataValue.length] = value[i];
                    }
                    for(let i=0; i<dataValue.length;i++)
                    {
                        if(dataValue[i]== 0xFF && dataValue[i+1] ==0xFF)
                        {
                              //该处需要进行特殊处理
                             if(dataValue[i+2]==0x04&& dataValue[i+3]==0xFA&& dataValue[i+4]==0x4F&& dataValue[i+5]==0x4B && dataValue[i+6]==0x98)
                             {
                                redResult=true;
                                dataValue.slice(i,6)
                                break;
                             }
                            //  else if(dataValue[i+2]!=0x04 && dataValue[i+3]==0xFA)
