                            //       dataSum += dataValue[i+4+j];
                            //     }
                            //     dataSum =dataSum%256;
                            //  }                                                        
                        }
                    }
                  }

                  var dataList =[];
                  //处理动作文件
                  for(let i=0;i<dataValue.length;i++)
                  {
                      if(dataValue[i]==0xFF&&dataValue[i+1]==0xFF&&dataValue[i+2]!=0xFF&&dataValue[i+3]==0x1A )
                      {
                          //获取长度
                          var dataCount = dataValue[i+2];
                          var dataInfo =[];
                          for(let j = 0; j<dataCount-2;j++)
                          {
                            dataInfo.push(dataValue[i+4+j]);
                          }
                          i=i+2+dataCount;
                           //转汉字，但是需要增加前缀
                          var strData ="地面::"+ iconv.decode((dataInfo),'gb2312');
                          dataList.push(strData);
                          //console.log(strData); 
                      }
                  }

                  //处理凌空的文件夹
                  nameByte = Array.from(iconv.encode("VolleyAction",'gb2312'));
                  length = nameByte.length;
                  sendLength = length +2; 
                  sendByte = [];
                  sendByte[0] = 0xFF;
                  sendByte[1] = 0xFF;
                  sendByte[2] = sendLength;
                  sendByte[3] = 0x1A;
                  sum = sendLength + 0x1A;
                  for(let i= 0;i<length;i++)
                  {
                      sendByte.push(nameByte[i]);
                      sum+=nameByte[i];
                  }
                  sum =sum%256;
                  sendByte.push(sum);
                  dataUt = new Uint8Array(sendByte);
                  await write.write(dataUt);
                  dataValue=[];
                  redResult = false;
                  while(!redResult)
                  {
                     const{value,done} = await reader.read();
                     if(done)
                     {
                        reader.releaseLock();
                        break;
                     }
                     for(let i=0; i<value.length;i++)
                     {
                        dataValue[dataValue.length] =value[i];
                     }
                     for(let i=0;i<dataValue.length;i++)
                     {
                        if(dataValue[i]== 0xFF && dataValue[i+1]==0xFF)
                        {
                          if(dataValue[i+2]==0x04&& dataValue[i+3]==0xFA&& dataValue[i+4]==0x4F&& dataValue[i+5]==0x4B && dataValue[i+6]==0x98)
                          {
                            redResult=true;
                            dataValue.slice(i,6)
                            break;
                          }
                        }
                      }
                  }

                  for(let i=0;i<dataValue.length;i++)
                  {
                    if(dataValue[i]==0xFF&&dataValue[i+1]==0xFF&&dataValue[i+2]!=0xFF&&dataValue[i+3]==0x1A )
                    {
                      var dataCount = dataValue[i+2];
                      var dataInfo =[];
                      for(let j = 0; j<dataCount-2;j++)
                      {
                        dataInfo.push(dataValue[i+4+j]);
                      }
                      i=i+2+dataCount;
                      var strData ="凌空::"+ iconv.decode((dataInfo),'gb2312');
                      dataList.push(strData);
                      //console.log(strData);
                    }
                  }
                  await write.releaseLock();
                  await reader.releaseLock();
                  this.vOpentions=dataList//

              }
            }
            catch(err)
            {
              console.error(err);
            }
