<template>
  <el-button @click="connectToDevice">连接蓝牙设备(钢锁)</el-button>
  <p></p>
  <el-button @click="UploadFileName">上传动作名称(龙型,人形两种)</el-button>
  <el-select v-model="selectActionName">
    <el-option
      v-for="actionName in actionNames"
      :key="actionName"
      :label="actionName"
      :value="actionName"
    >
    </el-option>
  </el-select>
  <el-button @click="performDance">播放动作</el-button>
  <p></p>
  <input type="file" @change="actionDownload" /> 下载
  <p></p>
  <el-button @click="GetMusic">获取机器人音乐</el-button>
</template>
<script>
import { ref, reactive } from "vue";
import iconv from "iconv-lite";
import { CreatePosDataDh } from "/src/PosDataGh.js";
export default {
  data() {
    return {
      device: [],
      server: [],
      isLink: false,
      characteristic: [],
      readDataList: [], //接收区
      actionNames: [], //动作名称
      selectActionName: "",
      isAction: false, //人型文件
      isRobotAction: false, //龙型文件
      actionExePorgress: 0, //动作执行进度
      isActionExePorgress: false, //动作执行是否开始
      sendPosDataNames: [], //发送数据的动作名称集
      sendPosData: [[]], //发送数据集
      isSendPosData: false, //发送点数据
      isSendPdFlag: 0, //发送数据的监听下标
      isSendPdFlagEach: 0, //每次发送数据的下标
      musicFlag: 0, //音乐
    };
  },
  methods: {
    async connectToDevice() {
      try {
        const options = {
          acceptAllDevices: true,
          // filters: [
          //   {
          //     namePrefix: "GSEG",
          //   },
          // ],
          optionalServices: [0xffe0],
        };
        await navigator.bluetooth.requestDevice(options).then(async (dev) => {
          this.device = dev;
          this.server = await this.device.gatt.connect();
          //console.log(this.server);
          await new Promise((resolve) => setTimeout(resolve, 1000));
          if (this.server.connected) {
            const services = await this.server.getPrimaryServices(0xffe0);
            // console.log(services);
            this.characteristic = await services[0].getCharacteristic(0xffe1);
            this.characteristic.startNotifications();
            this.characteristic.addEventListener(
              "characteristicvaluechanged",
              this.ReadDataMessage
            );
            this.isLink = true;

            //向机器人发动上传配置参数指令
            var data = [];
            data.push(0x0f); //发送上传参数让机器人播报数据
            this.SendDataFormatMessage(data);
            console.log("连接成功");
          } else {
            console.log("连接失败");
          }
        });
      } catch (err) {
        if (
          err instanceof DOMException &&
          err.code === DOMException.NOT_FOUND_ERR
        ) {
          this.isLink = false;
          console.log("连接设备出错:", err);
        } else {
          console.error("连接设备出错:", err);
          this.isLink = false;
        }
      }
    },
    async ReadDataMessage(data) {
      //接收池
      const dataArr = new Uint8Array(data.target.value.buffer);
      //console.log(dataArr);
      var len = dataArr.length;
      for (let i = 0; i < len; i++) {
        this.readDataList.push(dataArr[i]);
      }
      len = this.readDataList.length;
      // console.log(len);
      var readData = [];
      for (let i = 0; i < len; i++) {
        if (this.readDataList[i] == 0xff && this.readDataList[i + 1] == 0xff) {
          //获取长度如果长度存在
          var readLen = this.readDataList[i + 2];
          if (i + 3 + readLen <= len) {
            //判断长度是否存在
            //计算sum校验的值
            var sum = readLen;
            for (let j = 0; j < readLen - 1; j++) {
              sum += this.readDataList[i + 3 + j];
            }
            sum = sum % 256; //表示校验成功
            if (sum == this.readDataList[i + 2 + readLen]) {
              //  var flag = i + 2 + readLen + 1;
              var flag = 2 + readLen + 1;
              var dataFlag = [];
              for (let j = 0; j < flag; j++) {
                dataFlag.push(this.readDataList[i + j]);
              }
              i = i + 2 + readLen;
              readData.push(dataFlag);
              //console.log(flag);
              //console.log(dataFlag);
            }
          } else {
            //大于则直接退出循环开始下一次处理
            return;
          }
        }
      }

      if (readData.length > 0) {
        //移除
        for (let i = 0; i < readData.length; i++) {
          for (let j = 0; j < readData[i].length; j++) {
            this.readDataList.shift();
          }
        }
        //数据处理
        for (let i = 0; i < readData.length; i++) {
          // console.log(readData[i]);
          if (
            readData[i][0] == 0xff &&
            readData[i][1] == 0xff &&
            readData[i][3] == 0x0f
          ) {
          } //上传电量等配置参数(因为他是实时发的所以目前先不处理)
          else if (
            readData[i][0] == 0xff &&
            readData[i][1] == 0xff &&
            readData[i][3] == 0x10
          ) {
            //解析成汉字
            var dataInfo = readData[i].slice(4, readData[i].length - 1);
            console.log("2:" + dataInfo);
            this.actionNames.push("龙型:" + iconv.decode(dataInfo, "GB18030"));
          } //上传Action动作文件
          else if (
            readData[i][0] == 0xff &&
            readData[i][1] == 0xff &&
            readData[i][2] == 0x03 &&
            readData[i][3] == 0xfa &&
            readData[i][4] == 0xfa
          ) {
            //console.log(this.isRobotAction);
            if (this.isAction) {
              this.isAction = false;
              var data = [];
              data.push(0x16);
              var dataName = Array.from(iconv.encode("RobotAction", "GB18030"));
              for (let i = 0; i < dataName.length; i++) {
                data.push(dataName[i]);
              }
              this.isRobotAction = true;
              this.SendDataFormatMessage(data);
            }

            console.log("双Fa判断结果:" + this.musicFlag); //K1音乐返回
          } //上传Action结束码
          else if (
            readData[i][0] == 0xff &&
            readData[i][1] == 0xff &&
            readData[i][2] == 0x03 &&
            readData[i][3] == 0xfa &&
            readData[i][4] == 0xfd
          ) {
            console.log("fa fd判断结果:" + this.musicFlag); //K1音乐返回
          } //其他音乐返回
          else if (
            readData[i][0] == 0xff &&
            readData[i][1] == 0xff &&
            readData[i][3] == 0x16
          ) {
            //解析成汉字
            var dataInfo = readData[i].slice(4, readData[i].length - 1);
            this.actionNames.push("人型:" + iconv.decode(dataInfo, "GB18030"));
          } //指定文件夹上传文件
          else if (
            readData[i][0] == 0xff &&
            readData[i][1] == 0xff &&
            readData[i][2] == 0x03 &&
            readData[i][3] == 0xfa &&
            readData[i][4] == 0xfc
          ) {
            if (this.isRobotAction) {
              this.isRobotAction = false;
              //console.log(this.actionNames);
            }
          } //上传指定文件的结束吗
          else if (
            readData[i][0] == 0xff &&
            readData[i][1] == 0xff &&
            readData[i][3] == 0x17
          ) {
            this.actionExePorgress = readData[i][4];
            if (readData[i][4] == 100) {
              this.isActionExePorgress = false;
            }
          } //判断是否结束
          else if (
            readData[i][0] == 0xff &&
            readData[i][1] == 0xff &&
            readData[i][3] == 0xdc &&
            readData[i][4] == 0x4f &&
            readData[i][5] == 0x4b
          ) {
            //发送数据的首帧
            var data = this.sendPosData[this.isSendPdFlag][0];
            console.log(
              "第" + this.isSendPdFlag + "个动作文件创建成功，发送首帧"
            );
            this.SendDataFormatMessage(data);
          } //判断是否创建文件结束
          else if (
            readData[i][0] == 0xff &&
            readData[i][1] == 0xff &&
            readData[i][3] == 0xe3 &&
            readData[i][4] == 0x4f &&
            readData[i][5] == 0x4b
          ) {
            //需要判断是发送下一帧还是创建新的文件
            var len = this.sendPosData[this.isSendPdFlag].length;
            if (len - 1 == this.isSendPdFlagEach) {
              //说明该动作文件发送完成需要判断有没有下一个动作文件
              this.isSendPdFlag = this.isSendPdFlag + 1;
              if (this.sendPosDataNames.length == this.isSendPdFlag) {
                console.log("动作文件发送完成");
                this.isSendPosData = false;
              } else if (this.isSendPdFlag < this.sendPosDataNames.length) {
                //发送创建文件
                this.isSendPdFlagEach = 0;
                //发送创建文件
                var sendPosDataName = this.sendPosDataNames[this.isSendPdFlag];
                var nameByte = this.nameDataDispose(sendPosDataName);
                var sendData = [];
                sendData.push(0xdc);
                for (let i = 0; i < nameByte.length; i++) {
                  sendData.push(nameByte[i]);
                }
                console.log("发送第" + this.isSendPdFlag + "个动作文件");
                this.SendDataFormatMessage(sendData);
              }
            } else {
              this.isSendPdFlagEach = this.isSendPdFlagEach + 1;
              var data =
                this.sendPosData[this.isSendPdFlag][this.isSendPdFlagEach];
              console.log("发送第" + this.isSendPdFlagEach + "帧");
              this.SendDataFormatMessage(data);
            }
          } //下载文件结束
          else if (
            readData[i][0] == 0xff &&
            readData[i][1] == 0xff &&
            readData[i][3] == 0x18
          ) {
            var dataInfo = readData[i].slice(4, readData[i].length - 1);
            console.log(iconv.decode(dataInfo, "GB18030"));
            this.musicFlag++;
          } //读取
        }
      }
    },
    async SendDataFormatMessage(data) {
      var sendData = [];
      sendData.push(0xff);
      sendData.push(0xff);
      sendData.push(data.length + 1);
      var sum = data.length + 1;
      for (let i = 0; i < data.length; i++) {
        sendData.push(data[i]);
        sum += data[i];
      }
      sendData.push(sum % 256);
      await this.characteristic.writeValue(new Uint8Array(sendData));
    },
    async UploadFileName() {
      //上传动作文件名称先上传龙形在上传人形
      if (
        this.isLink &&
        this.isAction == false &&
        this.isRobotAction == false
      ) {
        this.actionNames = [];
        var data = [];
        data.push(0x10);
        this.isAction = true;
        this.SendDataFormatMessage(data);
      }
    },
    async performDance() {
      //播放动作
      if (
        this.isLink &&
        this.selectActionName != "" &&
        this.isActionExePorgress == false
      ) {
        this.isActionExePorgress = true;
        this.actionExePorgress = 0;
        var typeName = this.selectActionName.substring(0, 3);
        var actionName = this.selectActionName.substring(3);
        if (typeName == "人型:") {
          typeName = "RobotAction/";
        } else if (typeName == "龙型:") {
          typeName = "Action/";
        }
        actionName = typeName + actionName;
        var nameByte = Array.from(iconv.encode(actionName, "GB18030"));
        var data = [];
        data.push(0x17);
        for (let i = 0; i < nameByte.length; i++) {
          data.push(nameByte[i]);
        }
        // console.log(data);
        this.SendDataFormatMessage(data);
      }
    },
    async actionDownload(event) {
      var file = event.target.files[0];
      var reader = new FileReader();
      reader.readAsText(file, "UTF-8");
      reader.onload = async (evt) => {
        const fileString = evt.target.result;
        const count = fileString.trim().split("\n").length;
        var dataArr = [];
        for (let index = 0; index < count; index++) {
          const fileline = fileString.split("\r\n")[index].split(",")[0];
          const filelineNumber = fileline
            .split(/\s+/)
            .map((match) =>
              isNaN(match) ? match : match == "" ? " " : Number(match)
            );
          dataArr.push(filelineNumber);
        }

        if (this.isLink == true && this.isSendPosData == false) {
          this.isSendPosData = true;
          this.sendPosData = [[]];
          this.isSendPdFlag = 0;
          this.isSendPdFlagEach = 0;
          var nameArr = [];
          var dataAction = [];
          var len = dataArr.length;

          for (let i = 0; i < len; i++) {
            var _is_Action = false;
            if (dataArr[i][0] == " ") {
              _is_Action = true;
            }
            if (
              dataArr[i][0] == 253 &&
              dataArr[i][1] == 253 &&
              dataArr[i][2] == 254 &&
              dataArr[i][3] != " "
            ) {
              nameArr.push(dataArr[i][3]);
            }
            if (_is_Action == true) {
              dataAction.push(dataArr[i]);
            }
          }
          var last_loop_byte = 0;
          len = dataAction.length;
          var posDataArr = [];
          for (let i = 0; i < len; i++) {
            var _n1 = dataAction[i][5];
            var _n2 = dataAction[i][6];
            var _n3 = dataAction[i][7];
            var _n4 = dataAction[i][8];
            var _n5 = dataAction[i][9];
            var _n6 = dataAction[i][10];

            for (let j = 0; j < 8; j++) {
              let _Zero = _n1 & Math.pow(2, 7 - j);
              if (_Zero != 0) {
                dataAction[i][j + 11] *= -1;
              }
            }
            for (let j = 0; j < 8; j++) {
              let _Zero = _n2 & Math.pow(2, 7 - j);
              if (_Zero != 0) {
                dataAction[i][j + 19] *= -1;
              }
            }
            for (let j = 0; j < 8; j++) {
              let _Zero = _n3 & Math.pow(2, 7 - j);
              if (_Zero != 0) {
                dataAction[i][j + 27] *= -1;
              }
            }
            for (let j = 0; j < 8; j++) {
              let _Zero = _n4 & Math.pow(2, 7 - j);
              if (_Zero != 0) {
                dataAction[i][j + 35] *= -1;
              }
            }
            for (let j = 0; j < 8; j++) {
              let _Zero = _n5 & Math.pow(2, 7 - j);
              if (_Zero != 0) {
                dataAction[i][j + 43] *= -1;
              }
            }
            for (let j = 0; j < 8; j++) {
              let _Zero = _n6 & Math.pow(2, 7 - j);
              if (_Zero != 0) {
                dataAction[i][j + 51] *= -1;
              }
            }
            var Loop_Time = 0;
            var Is_Loop_Start = false;
            var Is_Loop_End = false;
            if (dataAction[i][4] != 0 && dataAction[i][4] > 128) {
              Loop_Time = dataAction[i][4] - 128;
            } else if (dataAction[i][4] != 0 && dataAction[i][4] < 128) {
              Loop_Time = dataAction[i][4];
              Is_Loop_Start = false;
              Is_Loop_End = true;
            } else if (dataAction[i][4] == 0) {
              Is_Loop_Start = false;
              Is_Loop_End = false;
            }
            var Pos = [];
            var number = dataAction[i][3];
            for (let j = 0; j < 48; j++) {
              Pos[j] = dataAction[i][j + 11];
            }
            var Speed = dataAction[i][59]; //速度
            var Delay = dataAction[i][60]; //延迟
            var Music_State = 0;
            var Music_Number = -1;
            var Music_Chosen = -1;
            var Is_Cur_Form = 0;
            var dActionLen = dataAction[i].length - 2;
            if (dActionLen == 63) {
              if (
                dataAction[i][61] != 251 &&
                dataAction[i][62] != 251 &&
                dataAction[i][61] != 252 &&
                dataAction[i][62] != 252
              ) {
                Music_Chosen = dataAction[i][61] * 256 + dataAction[i][62];
                Music_State = 2;
              } else if (dataAction[i][61] == 251 && dataAction[i][62] == 251) {
                Music_State = 4;
              } else if (dataAction[i][61] == 252 && dataAction[i][62] == 252) {
                Music_State = 3;
              }
              Music_Number = Music_Chosen;
            } else if (dActionLen == 61) {
              Music_Chosen = 0;
              Music_State = 2;
            }

            Is_Cur_Form = dataAction[i][dataAction[i].length - 2];
            Music_Number = Music_Chosen;
            if (dataAction[i][4] != 0) {
              if (last_loop_byte == 0) {
                Is_Loop_Start = true;
              } else {
                Is_Loop_Start = false;
              }
              if (dataAction[i][4] < 128) {
                Is_Loop_End = true;
                Loop_Time = dataAction[i][4];
              } else {
                Is_Loop_End = false;
                Loop_Time = dataAction[i][4] - 128;
              }
            }

            if (dataAction[i][4] > 128) {
              last_loop_byte = dataAction[i][4];
            } else {
              last_loop_byte = 0;
            }

            var posDataDhInit = {
              number: number,
              Name: 1,
              Pos: Pos,
              Speed: Speed,
              Delay: Delay,
              Loop_Time: Loop_Time,
              Is_Loop_Start: Is_Loop_Start,
              Is_Loop_End: Is_Loop_End,
              Music_State: Music_State,
              Music_Number: Music_Number,
              Is_Cur_Form: Is_Cur_Form,
            };
            posDataArr.push(CreatePosDataDh(posDataDhInit));
          }
          this.sendPosDataNames = nameArr;

          len = posDataArr.length;
          let currentNumber = 0;
          var Loop_Time = -1;
          var Is_In_Loop = false;
          var currentData = [[]];
          var flagNumber = posDataArr[0].number;
          currentData[0] = [];
          for (let i = 0; i < len; i++) {
            if (flagNumber != posDataArr[i].number) {
              flagNumber = posDataArr[i].number;
              currentNumber++;
              currentData[currentNumber] = [];
            }
            var currentPos = [];
            currentPos.push(0xe3);
            if (!Is_In_Loop) {
              currentPos[1] = 0;
            }
            if (
              Is_In_Loop &&
              !posDataArr[i].Is_Loop_Start &&
              !posDataArr[i].Is_Loop_End
            ) {
              currentPos[1] = Loop_Time + 128;
            }
            if (posDataArr[i].Is_Loop_End) {
              Is_In_Loop = false;
              currentPos[1] = Loop_Time;
              Loop_Time = 0;
            }
            if (posDataArr[i].Is_Loop_Start) {
              Is_In_Loop = true;
              Loop_Time = posDataArr[i].Loop_Time;
              currentPos[1] = Loop_Time + 128;
            }

            var _int_temp1 = 0;
            var _int_temp2 = 0;
            var _int_temp3 = 0;
            var _int_temp4 = 0;
            var _int_temp5 = 0;
            var _int_temp6 = 0;
            for (let k = 0; k < 48; k++) {
              if (k < 8) {
                if (posDataArr[i].Pos[k] < 0) {
                  _int_temp1 += parseInt(Math.pow(2, 7 - k));
                  posDataArr[i].Pos[k] = posDataArr[i].Pos[k] * -1;
                }
              }

              if (k >= 8 && k < 16) {
                if (posDataArr[i].Pos[k] < 0) {
                  _int_temp2 += parseInt(Math.pow(2, 15 - k));
                  posDataArr[i].Pos[k] = posDataArr[i].Pos[k] * -1;
                }
              }

              if (k >= 16 && k < 24) {
                if (posDataArr[i].Pos[k] < 0) {
                  _int_temp3 += parseInt(Math.pow(2, 23 - k));
                  posDataArr[i].Pos[k] = posDataArr[i].Pos[k] * -1;
                }
              }

              if (k >= 24 && k < 32) {
                if (posDataArr[i].Pos[k] < 0) {
                  _int_temp4 += parseInt(Math.pow(2, 31 - k));
                  posDataArr[i].Pos[k] = posDataArr[i].Pos[k] * -1;
                }
              }
              if (k >= 32 && k < 40) {
                if (posDataArr[i].Pos[k] < 0) {
                  _int_temp5 += parseInt(Math.pow(2, 39 - k));
                  posDataArr[i].Pos[k] = posDataArr[i].Pos[k] * -1;
                }
              }

              if (k >= 40 && k < 48) {
                if (posDataArr[i].Pos[k] < 0) {
                  _int_temp6 += parseInt(Math.pow(2, 47 - k));
                  posDataArr[i].Pos[k] = posDataArr[i].Pos[k] * -1;
                }
              }
            }

            currentPos[2] = _int_temp1;
            currentPos[3] = _int_temp2;
            currentPos[4] = _int_temp3;
            currentPos[5] = _int_temp4;
            currentPos[6] = _int_temp5;
            currentPos[7] = _int_temp6;
            for (let k = 0; k < 48; k++) {
              currentPos[k + 8] = posDataArr[i].Pos[k];
            }
            currentPos[56] = posDataArr[i].Speed;
            currentPos[57] = posDataArr[i].Delay;
            switch (posDataArr[i].Music_State) {
              case 1:
                currentPos[58] = parseInt(posDataArr[i].Music_Number / 256);
                currentPos[59] = parseInt(posDataArr[i].Music_Number % 256);
                break;
              case 2:
                currentPos[58] = parseInt(posDataArr[i].Music_Number / 256);
                currentPos[59] = parseInt(posDataArr[i].Music_Number % 256);
                break;
              case 3:
                currentPos[58] = 252;
                currentPos[59] = 252;
                break;
              case 4:
                currentPos[58] = 251;
                currentPos[59] = 251;
                break;
            }
            currentPos[60] = posDataArr[i].Is_Cur_Form;
            currentData[currentNumber].push(currentPos);
          }
          // console.log(currentData);
          this.sendPosData = currentData;

          //状态机模式发送数据
          var sendPosDataName = this.sendPosDataNames[0];
          var nameByte = this.nameDataDispose(sendPosDataName);
          var sendData = [];
          sendData.push(0xdc);
          for (let i = 0; i < nameByte.length; i++) {
            sendData.push(nameByte[i]);
          }
          console.log("创建首个动作文件");
          this.SendDataFormatMessage(sendData);
        } //是否连接
      };
    },
    nameDataDispose(nameData) {
      // console.log(nameData);
      var typeName = nameData.substring(0, 3);
      var actionName = nameData.substring(3);
      if (typeName == "人型:") {
        typeName = "RobotAction/";
      } else if (typeName == "龙型:") {
        typeName = "Action/";
      }
      actionName = typeName + actionName + ".sh";
      var nameByte = Array.from(iconv.encode(actionName, "GB18030"));
      return nameByte;
    },
    async GetMusic() {
      try {
        if (this.isLink) {
          this.musicFlag = 0;
          var data = [];
          data.push(0x18);
          var nameByte = Array.from(iconv.encode("21", "GB18030"));
          for (let i = 0; i < nameByte.length; i++) {
            data.push(nameByte[i]);
          }
          this.SendDataFormatMessage(data);
        }
      } catch (err) {
        console.error(err);
      }
    }, //获取音乐加中音乐的名称
  }, //方法集合
};
</script>
