}

async function connectToDevice() {
  try {
    // 请求蓝牙设备权限
    const options = {
      //acceptAllDevices: true,
      filters: [
        {
          namePrefix: "QTZM-",
        },
      ],
      optionalServices: [0xffc0],
    };
    // 连接蓝牙设备
    device = await navigator.bluetooth.requestDevice(options);
    server = await device.gatt.connect();
    await new Promise((resolve) => setTimeout(resolve, 1000)); // 延迟1秒钟

    // 获取蓝牙特征
    const services = await server.getPrimaryServices(0xffc0);
    console.log(services);

    characteristic = await services[0].getCharacteristic(0xffc1);
    console.log(characteristic);

    // 订阅数据
    characteristic.startNotifications();
    // characteristic.addEventListener("characteristicvaluechanged", (e) => {
    //   console.log(e.target.value);
    // });
    characteristic.addEventListener(
      "characteristicvaluechanged",
      ReadDataMessage
    );
    isLink = true;
    // 写入数据
    // await characteristic.writeValue(
    //   //   new Uint8Array([85, 170, 0, 5, 19, 242, 144, 156, 53])
    //   new Uint8Array([0x55, 0xaa, 0x00, 0x05, 0x13, 0xe7, 0x5f, 0x5d, 0xba])
    // );
  } catch (error) {
    console.error("连接设备出错:", error);
    isLink = false;
  }
}
