async function SendDataFormatMessage(data) {
  //发送消息格式化处理
  //先计算长度 在计算crc
  var sendData = [];
  var crc = 0xffff;
  var polynomial = 0x8005;
  var crcData = [];
  sendData.push(0x55);
  sendData.push(0xaa);
  var len = data.length + 3;
  sendData.push((len >> 8) & 0xff); //高位
  crcData.push((len >> 8) & 0xff);
  sendData.push(len & 0xff); //低位
  crcData.push(len & 0xff);
  for (let i = 0; i < data.length; i++) {
    sendData.push(data[i]);
    crcData.push(data[i]);
  }
  // for (let byte of crcData) {
  //   crc ^= byte << 8;
  //   for (let i = 0; i < 8; i++) {
  //     if (crc & 0x8000) {
  //       crc = (crc << 1) ^ polynomial;
  //     } else {
  //       crc <<= 1;
  //     }
  //     crc &= 0xffff;
  //   }
  // }

  for (let byte of crcData) {
    crc = (crc >> 8) ^ Crc16_Table[(crc ^ byte) & 0xff];
  }
  sendData.push((crc >> 8) & 0xff); //高位
  //sendData.push(crc / 256); //高位
  sendData.push(crc & 0xff); //低位
  //sendData.push(crc % 0xff); //低位
  var sum = 0;
  for (let i = 0; i < sendData.length; i++) {
    sum += sendData[i];
  }
  sendData.push(sum & 0xff);
  console.log(new Uint8Array(sendData));
  await characteristic.writeValue(new Uint8Array(sendData));
}
