<template>
  <el-button @click="connectToDevice">连接蓝牙设备(大黄蜂)</el-button>
  <p></p>
  <!-- <el-button @click="Advance">前进</el-button> -->
  <el-button @click="UploadActionFileNames">上传动作文件名</el-button>
  <el-select v-model="selectedOption">
    <el-option
      v-for="option in vOpentions"
      :key="option"
      :label="option"
      :value="option"
    >
    </el-option>
  </el-select>
  <el-button @click="performDance">播放动作</el-button>
  <p></p>
  <table>
    单独控制:进入编程模式 > 接收进入编程模式完成后 > 发送动作帧 > 退出编程模式
  </table>
  <p></p>
  <el-button @click="InProgrammingMode">进入编程模式</el-button>
  <el-input v-model="ArmInput" style="width: 50px"></el-input>
  <el-button @click="ControlJoints">实时位置</el-button>
  <p></p>
  <input type="file" @change="actionDownload" /> 下载
</template>
<script>
import { ref, reactive } from "vue";
import iconv from "iconv-lite";
import { CreatePosData } from "/src/PosData.js";
export default {
  data() {
    return {
      device: [],
      server: [],
      isLink: false,
      characteristic: [],
      Crc16_Table: [
        0x0000, 0xc0c1, 0xc181, 0x0140, 0xc301, 0x03c0, 0x0280, 0xc241, 0xc601,
        0x06c0, 0x0780, 0xc741, 0x0500, 0xc5c1, 0xc481, 0x0440, 0xcc01, 0x0cc0,
        0x0d80, 0xcd41, 0x0f00, 0xcfc1, 0xce81, 0x0e40, 0x0a00, 0xcac1, 0xcb81,
        0x0b40, 0xc901, 0x09c0, 0x0880, 0xc841, 0xd801, 0x18c0, 0x1980, 0xd941,
        0x1b00, 0xdbc1, 0xda81, 0x1a40, 0x1e00, 0xdec1, 0xdf81, 0x1f40, 0xdd01,
        0x1dc0, 0x1c80, 0xdc41, 0x1400, 0xd4c1, 0xd581, 0x1540, 0xd701, 0x17c0,
        0x1680, 0xd641, 0xd201, 0x12c0, 0x1380, 0xd341, 0x1100, 0xd1c1, 0xd081,
        0x1040, 0xf001, 0x30c0, 0x3180, 0xf141, 0x3300, 0xf3c1, 0xf281, 0x3240,
        0x3600, 0xf6c1, 0xf781, 0x3740, 0xf501, 0x35c0, 0x3480, 0xf441, 0x3c00,
        0xfcc1, 0xfd81, 0x3d40, 0xff01, 0x3fc0, 0x3e80, 0xfe41, 0xfa01, 0x3ac0,
        0x3b80, 0xfb41, 0x3900, 0xf9c1, 0xf881, 0x3840, 0x2800, 0xe8c1, 0xe981,
        0x2940, 0xeb01, 0x2bc0, 0x2a80, 0xea41, 0xee01, 0x2ec0, 0x2f80, 0xef41,
        0x2d00, 0xedc1, 0xec81, 0x2c40, 0xe401, 0x24c0, 0x2580, 0xe541, 0x2700,
        0xe7c1, 0xe681, 0x2640, 0x2200, 0xe2c1, 0xe381, 0x2340, 0xe101, 0x21c0,
        0x2080, 0xe041, 0xa001, 0x60c0, 0x6180, 0xa141, 0x6300, 0xa3c1, 0xa281,
        0x6240, 0x6600, 0xa6c1, 0xa781, 0x6740, 0xa501, 0x65c0, 0x6480, 0xa441,
        0x6c00, 0xacc1, 0xad81, 0x6d40, 0xaf01, 0x6fc0, 0x6e80, 0xae41, 0xaa01,
        0x6ac0, 0x6b80, 0xab41, 0x6900, 0xa9c1, 0xa881, 0x6840, 0x7800, 0xb8c1,
        0xb981, 0x7940, 0xbb01, 0x7bc0, 0x7a80, 0xba41, 0xbe01, 0x7ec0, 0x7f80,
        0xbf41, 0x7d00, 0xbdc1, 0xbc81, 0x7c40, 0xb401, 0x74c0, 0x7580, 0xb541,
        0x7700, 0xb7c1, 0xb681, 0x7640, 0x7200, 0xb2c1, 0xb381, 0x7340, 0xb101,
        0x71c0, 0x7080, 0xb041, 0x5000, 0x90c1, 0x9181, 0x5140, 0x9301, 0x53c0,
        0x5280, 0x9241, 0x9601, 0x56c0, 0x5780, 0x9741, 0x5500, 0x95c1, 0x9481,
        0x5440, 0x9c01, 0x5cc0, 0x5d80, 0x9d41, 0x5f00, 0x9fc1, 0x9e81, 0x5e40,
        0x5a00, 0x9ac1, 0x9b81, 0x5b40, 0x9901, 0x59c0, 0x5880, 0x9841, 0x8801,
        0x48c0, 0x4980, 0x8941, 0x4b00, 0x8bc1, 0x8a81, 0x4a40, 0x4e00, 0x8ec1,
        0x8f81, 0x4f40, 0x8d01, 0x4dc0, 0x4c80, 0x8c41, 0x4400, 0x84c1, 0x8581,
        0x4540, 0x8701, 0x47c0, 0x4680, 0x8641, 0x8201, 0x42c0, 0x4380, 0x8341,
        0x4100, 0x81c1, 0x8081, 0x4040,
      ], //crc校验表
      vOpentions: [], //文件表
      vOpentionsList: [], //文件表
      selectedOption: "",
      isOpention: false,
      isProgramme: false, //进入编程模式
      progData: [], //进入编程模式的数据集合
      ArmInput: 0,
      sendPosData: [[]], //发送数据集
      sendPosDataNames: [], //发送数据的动作名称集
      isSendPosData: false, //发送点数据
      isSendPdFlag: 0, //发送数据的监听下标
      isSendPdFlagEach: 0, //每次发送数据的下标
    };
  },
  methods: {
    async connectToDevice() {
      try {
        const options = {
          //acceptAllDevices: true,
          filters: [
            {
              namePrefix: "DHF",
            },
          ],
          optionalServices: [0xffc0],
          // optionalServices: [0xffe0], //这个是测别的不是大黄蜂
        };
        await navigator.bluetooth.requestDevice(options).then(async (dev) => {
          this.device = dev;
          console.log(this.device);
          this.server = await this.device.gatt.connect();

          await new Promise((resolve) => setTimeout(resolve, 1000));
          const services = await this.server.getPrimaryServices(0xffc0);
          this.characteristic = await services[0].getCharacteristic(0xffc1);
          // const services = await this.server.getPrimaryServices(0xffe0);
          // this.characteristic = await services[0].getCharacteristic(0xffe1);
          this.characteristic.startNotifications();
          this.characteristic.addEventListener(
            "characteristicvaluechanged",
            this.ReadDataMessage
          );
          this.isLink = true;
        });
      } catch (err) {
        if (
          err instanceof DOMException &&
          err.code === DOMException.NOT_FOUND_ERR
        ) {
          this.isLink = false;
          console.error("连接设备出错:", err);
        } else {
          console.error("连接设备出错:", err);
          this.isLink = false;
        }
      }
    },
    //接收
    async ReadDataMessage(data) {
      //console.log(data.target.value);
      const dataArr = new Uint8Array(data.target.value.buffer);
      console.log(dataArr);
      //得到数据后需要进行校验然crc后在统一的处理相对应的集合中
      var len = dataArr.length;
      for (let i = 0; i < len; i++) {
        if (dataArr[i] == 0x55 && dataArr[i + 1] == 0xaa) {
          //获取到了帧头，后获取数据总长度在计算
          //计算长度和判断指令、CRC、已经总数校验
          var high = dataArr[i + 2];
          var low = dataArr[i + 3];
          var getDataLen = (high << 8) | low; //数据长度
          var main_Cmd = dataArr[i + 4];
          var sub_Cmd = dataArr[i + 5];
          if (main_Cmd == 0x13 && sub_Cmd == 0x10) {
            //计算CRC校验

            var crcHigh = dataArr[i + 3 + getDataLen - 2];
            var crcLow = dataArr[i + 3 + getDataLen - 1];
            var crcValue = (crcHigh << 8) | crcLow;
            var crc = 0xffff;
            for (let j = 0; j < getDataLen - 1; j++) {
              crc =
                (crc >> 8) ^
                this.Crc16_Table[(crc ^ dataArr[i + 2 + j]) & 0xff];
            }
            //console.log("处理2:" + crc);
            if (crc == crcValue) {
              //校验通过
              //开始处理数据
              var actionData = [];
              for (let j = 0; j < getDataLen - 3 - 2; j++) {
                actionData.push(dataArr[i + 6 + j]);
              }
              // this.vOpentions.push();
              this.addVoptions(iconv.decode(actionData, "GB18030"));
            } else {
              //校验失败直接不处理或者按照类型进行区别处理
            }
          } else if (
            main_Cmd == 0x13 &&
            sub_Cmd == 0xfa &&
            dataArr[i + 6] == 0xfa
          ) {
            if (this.isOpention) {
              this.isOpention = false;
              console.log(this.vOpentions);
            }
          } else if (main_Cmd == 0x13 && sub_Cmd == 0xe6) {
            if (dataArr[i + 6] == 0xe6 && getDataLen == 6) {
              this.isProgramme = true;
              console.log(this.progData);
            } else {
              var crcHigh = dataArr[i + 3 + getDataLen - 2];
              var crcLow = dataArr[i + 3 + getDataLen - 1];
              var crcValue = (crcHigh << 8) | crcLow;
              var crc = 0xffff;
              for (let j = 0; j < getDataLen - 1; j++) {
                crc =
                  (crc >> 8) ^
                  this.Crc16_Table[(crc ^ dataArr[i + 2 + j]) & 0xff];
              }
              if (crc == crcValue) {
                var actionData = [];
                for (let j = 0; j < getDataLen - 3 - 2; j++) {
                  actionData.push(dataArr[i + 6 + j]);
                }
                this.progData.push(actionData);
              } else {
              }
            }
          } //进入编程模式结束
          else if (
            main_Cmd == 0x13 &&
            sub_Cmd == 0xdc &&
            dataArr[i + 6] == 0x4f &&
            dataArr[i + 7] == 0x4b
          ) {
            var data = this.sendPosData[this.isSendPdFlag][0];
            console.log(
              "第" + this.isSendPdFlag + "个动作文件创建成功，发送首帧"
            );
            this.SendDataFormatMessage(data);
          } //判断创建文件完成
          else if (
            main_Cmd == 0x13 &&
            sub_Cmd == 0xe3 &&
            dataArr[i + 6] == 0x4f &&
            dataArr[i + 7] == 0x4b
          ) {
            var len = this.sendPosData[this.isSendPdFlag].length;
            if (len - 1 == this.isSendPdFlagEach) {
              this.isSendPdFlag = this.isSendPdFlag + 1;
              if (this.sendPosDataNames.length == this.isSendPdFlag) {
                console.log("动作文件发送完成");
                this.isSendPosData = false;
              } else if (this.isSendPdFlag < this.sendPosDataNames.length) {
                this.isSendPdFlagEach = 0;
                var sendPosDataName = this.sendPosDataNames[this.isSendPdFlag];
                var nameByte = this.nameDataDispose(sendPosDataName);
                var sendData = [];
                var sendData = [];
                sendData.push(0x13);
                sendData.push(0xdc);
                for (let i = 0; i < nameByte.length; i++) {
                  sendData.push(nameByte[i]);
                }
                console.log("发送第" + this.isSendPdFlag + "个动作文件");
                this.SendDataFormatMessage(sendData);
              }
            } else {
              this.isSendPdFlagEach = this.isSendPdFlagEach + 1;
              var data =
                this.sendPosData[this.isSendPdFlag][this.isSendPdFlagEach];
              console.log("发送第" + this.isSendPdFlagEach + "帧");
              this.SendDataFormatMessage(data);
            }
          }
        }
      }
    }, //接收
    async SendDataFormatMessage(data) {
      var sendData = [];
      var crc = 0xffff;
      var crcData = [];
      sendData.push(0x55);
      sendData.push(0xaa);
      var len = data.length + 3;
      sendData.push((len >> 8) & 0xff);
      crcData.push((len >> 8) & 0xff);
      sendData.push(len & 0xff);
      crcData.push(len & 0xff);
      for (let i = 0; i < data.length; i++) {
        sendData.push(data[i]);
        crcData.push(data[i]);
      }
      for (let byte of crcData) {
        crc = (crc >> 8) ^ this.Crc16_Table[(crc ^ byte) & 0xff];
      }
      sendData.push((crc >> 8) & 0xff); //高位
      sendData.push(crc & 0xff); //低位
      // console.log((crc >> 8) & 0xff);
      // console.log(crc & 0xff);
      var sum = 0;
      for (let i = 0; i < sendData.length; i++) {
        sum += sendData[i];
      }
      sendData.push(sum & 0xff);
      // console.log(new Uint8Array(sendData));
      await this.characteristic.writeValue(new Uint8Array(sendData));
    }, //发送
    async Advance() {
      //前进
      var data = [0x13, 0x01];
      this.SendDataFormatMessage(data);
    },
    async UploadActionFileNames() {
      //上传动作文件名
      if (this.isLink) {
        var data = [0x13, 0x10];
        this.isOpention = true;
        this.vOpentions = [];
        this.vOpentionsList = [];
        this.SendDataFormatMessage(data);
      }
    },
    addVoptions(option) {
      // this.vOpentionsList.push(option);
      //this.vOpentions = this.vOpentionsList;
      //Action
      this.vOpentions.push(option);
    },
    async performDance() {
      //执行舞蹈动作
      if (this.isLink && this.selectedOption != "") {
        var actionName = Array.from(
          iconv.encode("Action/" + this.selectedOption, "GB18030")
        );
        var data = [];
        data.push(0x13);
        data.push(0x17);
        for (let i = 0; i < actionName.length; i++) {
          data.push(actionName[i]);
        }
        this.SendDataFormatMessage(data);
      }
    },
    async InProgrammingMode() {
      //进入编程模式
      console.log(11);
      if (this.isLink) {
        console.log(2);
        var data = [];
        data.push(0x13);
        data.push(0xe6);
        this.isProgramme = false;
        this.SendDataFormatMessage(data);
      }
    },
    async ControlJoints() {
      //控制关节到指定位置
      // if (this.isLink) {
      //   var data = Array.from(this.progData[0]);
      //   data[0] += parseInt(this.ArmInput);
      //   var senData = [];
      //   senData.push(0x13);
      //   senData.push(0xe8);
      //   for (let i = 0; i < data.length; i++) {
      //     senData.push(data[i]);
      //   }
      //   senData.push(0x19);
      //   this.SendDataFormatMessage(senData);
      // }

      var data = [
        0x03, 0x13, 0xff, 0xff, 0x39, 0xf1, 0x16, 0x48, 0x09, 0x00, 0x00, 0x00,
        0x14, 0x00, 0x00, 0x02, 0x0c, 0x23, 0x05, 0x00, 0x00, 0x04, 0x00, 0x2b,
        0x04, 0x5b, 0x41, 0x00, 0x00, 0x00, 0x00, 0x00, 0x55, 0x00, 0x00, 0x05,
        0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00,
        0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00,
        0x0a, 0x0e,
      ];
      this.SendDataFormatMessage(data);
    },
    async actionDownload(event) {
      try {
        const file = event.target.files[0];
        const reader = new FileReader();
        reader.readAsText(file, "UTF-8");
        reader.onload = async (evt) => {
          const fileString = evt.target.result;
          const count = fileString.trim().split("\n").length;
          this.dataArr = [];
          for (let index = 0; index < count; index++) {
            const fileline = fileString.split("\r\n")[index].split(",")[0];
            const filelineNumber = fileline
              .split(/\s+/)
              .map((match) =>
                isNaN(match) ? match : match == "" ? " " : Number(match)
              );
            this.dataArr.push(filelineNumber);
          }
          if (this.isLink == true && this.isSendPosData == false) {
            this.isSendPosData = true;
            this.sendPosData = [[]];
            this.isSendPdFlag = 0;
            this.isSendPdFlagEach = 0;
            var nameArr = [];
            var dataAction = [];
            var dataArr = this.dataArr;
            var len = dataArr.length;
            for (let i = 0; i < len; i++) {
              var _is_Action = false;
              if (dataArr[i][0] == " ") {
                _is_Action = true;
              }
              if (
                dataArr[i][0] == 253 &&
                dataArr[i][1] == 253 &&
                dataArr[i][2] == 254 &&
                dataArr[i][3] != " "
              ) {
                nameArr.push(dataArr[i][3]);
              }
              if (_is_Action == true) {
                dataAction.push(dataArr[i]);
              }
            }
            var last_loop_byte = 0;
            len = dataAction.length;
            var posDataArr = [];
            for (let i = 0; i < len; i++) {
              var _n1 = dataAction[i][5];
              var _n2 = dataAction[i][6];
              var _n3 = dataAction[i][7];
              var _n4 = dataAction[i][8];
              var _n5 = dataAction[i][9];
              var _n6 = dataAction[i][10];
              for (let j = 0; j < 8; j++) {
                let _Zero = _n1 & Math.pow(2, 7 - j);
                if (_Zero != 0) {
                  dataAction[i][j + 11] *= -1;
                }
              }
              for (let j = 0; j < 8; j++) {
                let _Zero = _n2 & Math.pow(2, 7 - j);
                if (_Zero != 0) {
                  dataAction[i][j + 19] *= -1;
                }
              }
              for (let j = 0; j < 8; j++) {
                let _Zero = _n3 & Math.pow(2, 7 - j);
                if (_Zero != 0) {
                  dataAction[i][j + 27] *= -1;
                }
              }
              for (let j = 0; j < 8; j++) {
                let _Zero = _n4 & Math.pow(2, 7 - j);
                if (_Zero != 0) {
                  dataAction[i][j + 35] *= -1;
                }
              }
              for (let j = 0; j < 8; j++) {
                let _Zero = _n5 & Math.pow(2, 7 - j);
                if (_Zero != 0) {
                  dataAction[i][j + 43] *= -1;
                }
              }
              for (let j = 0; j < 8; j++) {
                let _Zero = _n6 & Math.pow(2, 7 - j);
                if (_Zero != 0) {
                  dataAction[i][51] *= -1;
                }
              }
              var Loop_Time = 0;
              var Is_Loop_Start = false;
              var Is_Loop_End = false;
              if (dataAction[i][4] != 0 && dataAction[i][4] > 128) {
                Loop_Time = dataAction[i][4] - 128;
              } else if (dataAction[i][4] != 0 && dataAction[i][4] > 128) {
                Loop_Time = dataAction[i][4];
                Is_Loop_Start = false;
                Is_Loop_End = true;
              } else if (dataAction[i][4] == 0) {
                Is_Loop_Start = false;
                Is_Loop_End = false;
              }
              var Pos = [];
              var number = dataAction[i][3];
              for (let j = 0; j < 48; j++) {
                Pos[j] = dataAction[i][j + 11];
              }
              var Speed = dataAction[i][59];
              var Delay = dataAction[i][60];
              var Music_State = 0;
              var Music_Number = -1;
              var Music_Chosen = -1;
              if (
                dataAction[i][61] != 251 &&
                dataAction[i][62] != 251 &&
                dataAction[i][61] != 252 &&
                dataAction[i][62] != 252
              ) {
                Music_Chosen = dataAction[i][61] * 256 + dataAction[i][62];
                Music_State = 2;
              } else if (dataAction[i][61] == 251 && dataAction[i][62] == 251) {
                Music_State = 4;
              } else if (dataAction[i][61] == 252 && dataAction[i][62] == 252) {
                Music_State = 3;
              }
              Music_Number = Music_Chosen;
              if (dataAction[i][4] != 0) {
                if (last_loop_byte == 0) {
                  Is_Loop_Start = true;
                } else {
                  Is_Loop_Start = false;
                }
                if (dataAction[i][4] < 128) {
                  Is_Loop_End = true;
                  Loop_Time = dataAction[i][4];
                } else {
                  Is_Loop_End = false;
                  Loop_Time = dataAction[i][4] - 128;
                }
              }
              if (dataAction[i][4] > 128) {
                last_loop_byte = dataAction[i][4];
              } else {
                last_loop_byte = 0;
              }
              var posData = {
                number: number,
                Name: 1,
                Pos: Pos,
                Speed: Speed,
                Delay: Delay,
                Loop_Time: Loop_Time,
                Is_Loop_Start: Is_Loop_Start,
                Is_Loop_End: Is_Loop_End,
                Music_State: Music_State,
                Music_Number: Music_Number,
              };
              posDataArr.push(CreatePosData(posData));
            }
            this.sendPosDataNames = nameArr;
            len = posDataArr.length;
            let currentnumber = 0;
            var Loop_Time = -1;
            var Is_In_Loop = false;
            var currentData = [[]];
            var flagNumber = posDataArr[0].number;
            currentData[0] = [];
            for (let i = 0; i < len; i++) {
              if (flagNumber != posDataArr[i].number) {
                flagNumber = posDataArr[i].number;
                currentnumber++;
                currentData[currentnumber] = [];
              }
              var currentPos = [];
              currentPos.push(0x13);
              currentPos.push(0xe3);
              if (!Is_In_Loop) {
                currentPos[2] = 0;
              }
              if (
                Is_In_Loop &&
                !posDataArr[i].Is_Loop_Start &&
                !posDataArr[i].Is_Loop_End
              ) {
                currentPos[2] = Loop_Time + 128;
              }
              if (posDataArr[i].Is_Loop_End) {
                Is_In_Loop = false;
                currentPos[2] = Loop_Time;
                Loop_Time = 0;
              }
              if (posDataArr[i].Is_Loop_Start) {
                Is_In_Loop = true;
                Loop_Time = posDataArr[i].Loop_Time;
                currentPos[2] = Loop_Time + 128;
              }
              var _int_temp1 = 0;
              var _int_temp2 = 0;
              var _int_temp3 = 0;
              var _int_temp4 = 0;
              var _int_temp5 = 0;
              var _int_temp6 = 0;

              for (let k = 0; k < 48; k++) {
                if (k < 8) {
                  if (posDataArr[i].Pos[k] < 0) {
                    _int_temp1 += parseInt(Math.pow(2, 7 - k));
                    posDataArr[i].Pos[k] = posDataArr[i].Pos[k] * -1;
                  }
                }
                if (k >= 8 && k < 16) {
                  if (posDataArr[i].Pos[k] < 0) {
