//机器人选择实现层
import SelectRobotFactory from "/src/factory/SelectRobotFactory.js";
import OptimusPrime30Robot from "/src/factory/OptimusPrime30Robot.js";
import OptimusPrime40Robot from "/src/factory/OptimusPrime40Robot.js";
import { InfoEnum } from "/src/factory/InfoEnum.js";

var curverRobot = null;
export default class SelectRobotElect extends SelectRobotFactory {
  async BlueToothDispose() {
    try {
      const options = {
        // acceptAllDevices: true,
        filters: [
          {
            namePrefix: "K1", //k1机器人
          },
          {
            namePrefix: "OP", //40擎天柱
          },
          {
            namePrefix: "GSEG", //刚锁
          },
          {
            namePrefix: "CTGEN", //30擎天柱
          },
        ],
        optionalServices: [0xffe0],
      };
      var ret = null;
      await navigator.bluetooth.requestDevice(options).then(async (dev) => {
        // server = await this.device.gatt.connect();
        // await new Promise((resolve) => setTimeout(resolve, 1000));

        if (dev.name.startsWith("OP")) {
          //40擎天柱

          curverRobot = this.GetFactory(0);
        } else if (dev.name.startsWith("CTGEN")) {
          //30擎天柱
          curverRobot = this.GetFactory(1);
        } else {
          curverRobot = null;
        }
        if (curverRobot != null) {
          ret = await curverRobot.RobotBlueToothConntect(dev);
          //console.log(ret);
        } else {
          ret = { BlueLink: InfoEnum.fail, Meg: "无该型号机器人" }; //先测试，这里需要一个返回枚举集合吧表
        }
      });
      return ret;
    } catch (err) {
      return { BlueLink: InfoEnum.isErr, Meg: err };
    }
  } //蓝牙处理

  GetFactory(number) {
    var functionObj = null;
    switch (number) {
      case 0: //40擎天柱
        functionObj = new OptimusPrime40Robot();
        break;
      case 1: //30擎天柱
        functionObj = new OptimusPrime30Robot();
        break;
      default:
        functionObj = null;
        break;
    }
    return functionObj;
  } //用于实现具体工厂
}
