//30擎天柱
import RobotAbsFactory from "/src/factory/RobotAbsFactory.js";
import { InfoEnum } from "/src/factory/InfoEnum.js";
var serverBT = null;
var characteristic = null;
var isBlueLink = false; //是否连接
var isBT_Progeam = false; //是否进入蓝牙编译模式
var readDataList = []; //接收池
var sendDataList = []; //发送池
var isReadTimer = null; //发送数据的计时器
var isBTlinkTimer = null; //蓝牙断开处理计时器

export default class OptimusPrime30Robot extends RobotAbsFactory {
  async RobotBlueToothConntect(device) {
    try {
      serverBT = null;
      serverBT = await device.gatt.connect();
      await new Promise((resolve) => setTimeout(resolve, 1000));
      characteristic = null;
      isBlueLink = false;
      sendDataList = [];
      readDataList = [];

      if (serverBT.connected) {
        const services = await serverBT.getPrimaryServices(0xffe0);
        characteristic = await services[0].getCharacteristic(0xffe1);
        characteristic.startNotifications();
        characteristic.addEventListener(
          "characteristicvaluechanged",
          this.ReadDataMessage
        ); //蓝牙的接听事件,并且注册接收池
        //蓝牙被动断开的事件处理
        //serverBT.addEventListener("gattserverdisconnected", this.onDisconnected);
        //serverBT.ongattserverdisconnected = this.onDisconnected;
        isBlueLink = true;
        isReadTimer = new Promise((resolve, reject) => {
          const intervalid = setInterval(() => {
            this.SendDataMessage;
          }, 80);
        }); //计时器的方式注册发送池

        isBTlinkTimer = setInterval(() => {
          if (!device.gatt.connected) {
            clearInterval(isBTlinkTimer);
            // 蓝牙设备连接已断开，进行相应处理
            this.onDisconnected();
          }
        }, 1000);

        return {
          BlueLink: InfoEnum.succeed,
          Meg: "连接成功！",
          instance: this,
        };
      } else {
        return {
          BlueLink: InfoEnum.fail,
          Meg: "链接失败",
        };
      }
    } catch (err) {
      isBlueLink = false;
      return { BlueLink: InfoEnum.isErr, Meg: err };
    }
  } //连接蓝牙

  async ReadDataMessage(data) {
    const dataArr = new Uint8Array(data.target.value.buffer);
    var len = dataArr.length;
    for (let i = 0; i < len; i++) {
      readDataList.push(dataArr[i]);
    }
    len = readDataList.length;
    var readData = [];
    for (let i = 0; i < len; i++) {
      if (readDataList[i] == 0xff && readDataList[i + 1] == 0xff) {
        var readLen = readDataList[i + 2];
        if (i + 3 + readLen <= len) {
          var sum = readLen;
          for (let j = 0; j < readLen - 1; j++) {
            sum += readDataList[i + 3 + j];
          }
          sum = sum % 256;
          if (sum == readDataList[i + 2 + readLen]) {
            var flag = 2 + readLen + 1;
            var dataFlag = [];
            for (let j = 0; j < flag; j++) {
              dataFlag.push(readDataList[i + j]);
            }
            i = i + 2 + readLen;
            readData.push(dataFlag);
          }
        } else {
          return;
        }
      }
    }
    if (readData.length > 0) {
      //移除
      for (let i = 0; i < readData.length; i++) {
        for (let j = 0; j < readData[i].length; j++) {
          readDataList.shift();
        }
      }

      for (let i = 0; i < readData.length; i++) {
        console.log(readData[i]);
      }
    }
  } //接收池
  async SendDataMessage() {
    try {
      if (isBlueLink == true) {
        var dataLen = sendDataList.length;
        for (let i = 0; i < dataLen; i++) {
          let promise = new Promise((resolve, reject) => {
            setTimeout(() => resolve(), 50);
          });
          await promise;
          var data = sendDataList.shift();
          await characteristic.writeValue(new Uint8Array(data));
        }
      }
    } catch (err) {
      console.error(err);
    }
  } //发送数据池

  SendDataFormat(data) {
    var sendData = [];
    sendData.push(0xff);
    sendData.push(0xff);
    sendData.push(data.length + 1);
    var sum = data.length + 1;
    for (let i = 0; i < data.length; i++) {
      sendData.push(data[i]);
      sum += data[i];
    }
    sendData.push(sum % 256);
    sendDataList.push(sendData);
  } //发送数据处理

  onDisconnected() {
    //触发蓝牙主动断开
    clearInterval(isReadTimer);
    // serverBT.removeEventListener("gattserverdisconnected", this.onDisconnected);
    characteristic.removeEventListener(
      "characteristicvaluechanged",
      this.ReadDataMessage
    );
    sendDataList = [];
    readDataList = [];
    isBlueLink = null;
    characteristic = null;
    serverBT = null;
    console.log("蓝牙已经断开");
  } //蓝牙断开时的处理

  async RobotBlueToothBreak() {
    try {
      const isBluetoothAvailable = await navigator.bluetooth.getAvailability();
      if (isBluetoothAvailable) {
        serverBT.disconnect();
        clearInterval(isBTlinkTimer);
        this.onDisconnected();
        return {
          BlueLink: InfoEnum.succeed,
          Meg: "断开成功！",
        };
      }
    } catch (err) {
      return {
        BlueLink: InfoEnum.isErr,
        Meg: err,
      };
    }
  } //蓝牙断开

  async IntoBlueToothProgram() {
    try {
      if (isBlueLink) {
        isBT_Progeam = false;
        var timeoutId;
        //发送指令
        var data = [];
        data.push(0xe6);
        const result = new Promise((resolve, reject) => {
          const intervalid = setInterval(() => {
            if (isBT_Progeam == true) {
              clearInterval(intervalid);
              clearTimeout(timeoutId);
              resolve();
            }
          }, 100);
          timeoutId = setTimeout(() => {
            clearInterval(intervalid);
          }, 10000);
        });

        result.then(() => {
          if (isBT_Progeam) {
            //返回基础参数
          } else {
            return { BlueLink: InfoEnum.ununited, Meg: null };
          }
        });
      } else {
        return { BlueLink: InfoEnum.ununited, Meg: null };
      }
    } catch (err) {
      return { BlueLink: InfoEnum.fail, Meg: err };
    }
  } //进入蓝牙编程模式
}
