<template>
  <h3>钢索机器人通讯结构</h3>
  <p>连接方式 ： HID(usb口的一种方式)</p>
  <p>匹配VendorID ： 0x0483</p>
  <p>匹配ProductID ： 0x5750</p>
  <el-button type="primary" v-on:click="requestSerialPort()">连接</el-button>
  <label
    >连接成功后发送[0xFF, 0x02, 0xF5, 0xF7]读取数据直到获取到[4F
    4B](OK)后接收结束</label
  >
  <p></p>
  <el-button type="primary" v-on:click="closeMessage()">关闭</el-button>
  <label>关闭需要发送指令 [ 0xFF, 0x02, 0xF6, 0xF8] 然后断开HID</label>
  <p></p>
  <el-button v-on:click="timeSendingPosition()">时时发送位置</el-button>
  <label>例如左胯:</label>
  <el-input v-model="ArmInput" style="width: 50px"></el-input>
  <label
    >控制舵机移动[0xFF,数据长度,指令(0x01),数据(位置+速度),sum和]指定左肩的下标控制</label
  >
  <p></p>
  <input type="file" @change="actionDownload" /> 下载功能
  <p></p>
  <h4>上传功能</h4>
  <label>读取机器人内部的动作文件名</label>
  <p></p>
  <el-button type="primary" v-on:click="getActionName()">读取</el-button>
</template>
<script>
import { CreatePosDataDh } from "/src/PosDataGh.js";
import iconv from "iconv-lite";
export default {
  data() {
    return {
      initData: [], //存放4个初始位置
      isInitData: false, //是否接收完成
      endpointNumber: 2, //这个是要通讯的口
      isLink: false, //是否连接
      hidDevice: [], //通讯用
      dataArr: [],
      ArmInput: 0,
      isRedActionLoong: false,
      isRedActionRobot: false,
      actionList: [],
    };
  },
  methods: {
    async requestSerialPort() {
      //连接
      try {
        await navigator.hid
          .requestDevice({ filters: [{ vendorId: 0x0483, productId: 0x5750 }] }) //条件筛选
          .then(async (device) => {
            await device[0].open(); //打开
            this.hidDevice = device[0];
            device[0].addEventListener("inputreport", this.receivingProcess);
            this.initData = [];
            this.isInitData = false;
            var data = new Uint8Array([0xff, 0x02, 0xf5, 0xf7]); //深坑那个0 在这里是通道的id
            await device[0].sendReport(0, data);
          })
          .catch((error) => {
            console.error(error);
          });
      } catch (err) {
        console.log(err);
      }
    },
    async closeMessage() {
      //退出pc编译器
      if (this.isLink == true) {
        var data = new Uint8Array([0xff, 0x02, 0xf6, 0xf8]);
        await this.hidDevice.sendReport(0, data);
      }
      //this.hidDevice.close();
    },
    async actionDownload(
      event //动作下载
    ) {
      var file = event.target.files[0];
      var reader = new FileReader();
      reader.readAsText(file, "UTF-8");
      reader.onload = async (evt) => {
        const fileString = evt.target.result;
        const count = fileString.trim().split("\n").length;
        this.dataArr = [];
        for (let index = 0; index < count; index++) {
          const fileline = fileString.split("\r\n")[index].split(",")[0];
          const filelineNumber = fileline
            .split(/\s+/)
            .map((match) =>
              isNaN(match) ? match : match == "" ? " " : Number(match)
            );
          this.dataArr.push(filelineNumber);
        }
        // if(this.isLink == true)
        {
          //该处动作多了一个龙型人型判断
          var nameArr = [];
          var dataAction = [];
          var dataArr = this.dataArr;
          var len = dataArr.length;
          for (let i = 0; i < len; i++) {
            var _is_Action = false;
            if (dataArr[i][0] == " ") {
              _is_Action = true;
            }
            if (
              dataArr[i][0] == 253 &&
              dataArr[i][1] == 253 &&
              dataArr[i][2] == 254 &&
              dataArr[i][3] != " "
            ) {
              //名称
              nameArr.push(dataArr[i][3]);
            }
            if (_is_Action == true) {
              dataAction.push(dataArr[i]);
            }
          }

          var last_loop_byte = 0;
          len = dataAction.length;
          var posDataArr = [];
          for (let i = 0; i < len; i++) {
            var _n1 = dataAction[i][5];
            var _n2 = dataAction[i][6];
            var _n3 = dataAction[i][7];
            var _n4 = dataAction[i][8];
            var _n5 = dataAction[i][9];
            var _n6 = dataAction[i][10];
            for (let j = 0; j < 8; j++) {
              let _Zero = _n1 & Math.pow(2, 7 - j);
              if (_Zero != 0) {
                dataAction[i][j + 11] *= -1;
              }
            }
            for (let j = 0; j < 8; j++) {
              let _Zero = _n2 & Math.pow(2, 7 - j);
              if (_Zero != 0) {
                dataAction[i][j + 19] *= -1;
              }
            }
            for (let j = 0; j < 8; j++) {
              let _Zero = _n3 & Math.pow(2, 7 - j);
              if (_Zero != 0) {
                dataAction[i][j + 27] *= -1;
              }
            }
            for (let j = 0; j < 8; j++) {
              let _Zero = _n4 & Math.pow(2, 7 - j);
              if (_Zero != 0) {
                dataAction[i][j + 35] *= -1;
              }
            }
            for (let j = 0; j < 8; j++) {
              let _Zero = _n5 & Math.pow(2, 7 - j);
              if (_Zero != 0) {
                dataAction[i][j + 43] *= -1;
              }
            }
            for (let j = 0; j < 8; j++) {
              let _Zero = _n6 & Math.pow(2, 7 - j);
              if (_Zero != 0) {
                dataAction[i][j + 51] *= -1;
              }
            }
            var Loop_Time = 0;
            var Is_Loop_Start = false;
            var Is_Loop_End = false;
            //用于判断是不是循环
            if (dataAction[i][4] != 0 && dataAction[i][4] > 128) {
              Loop_Time = dataAction[i][4] - 128;
            } else if (dataAction[i][4] != 0 && dataAction[i][4] < 128) {
              Loop_Time = dataAction[i][4];
              Is_Loop_Start = false;
              Is_Loop_End = true;
            } else if (dataAction[i][4] == 0) {
              Is_Loop_Start = false;
              Is_Loop_End = false;
            }
            var Pos = [];
            var number = dataAction[i][3];
            for (let j = 0; j < 48; j++) {
              Pos[j] = dataAction[i][j + 11];
            }
            var Speed = dataAction[i][59]; //速度
            var Delay = dataAction[i][60]; //延迟
            var Music_State = 0;
            var Music_Number = -1;
            var Music_Chosen = -1;
            var Is_Cur_Form = 0;
            //这里钢索与其他的不同之处//数据解析 前后都有空格
            //  console.log(dataAction[i].length);
            //  console.log(dataAction[i]);
            var dActionLen = dataAction[i].length - 2; //去掉前后空格
            if (dActionLen == 63) {
              if (
                dataAction[i][61] != 251 &&
                dataAction[i][62] != 251 &&
                dataAction[i][61] != 252 &&
                dataAction[i][62] != 252
              ) {
                Music_Chosen = dataAction[i][61] * 256 + dataAction[i][62];
                Music_State = 2;
              } else if (dataAction[i][61] == 251 && dataAction[i][62] == 251) {
                Music_State = 4;
              } else if (dataAction[i][61] == 252 && dataAction[i][62] == 252) {
                Music_State = 3;
              }
              Music_Number = Music_Chosen;
            } else if (dActionLen == 61) {
              Music_Chosen = 0;
              Music_State = 2;
            }
            // console.log(dataAction[i]);
            // console.log(dataAction[i][dataAction.length-2]);
            Is_Cur_Form = dataAction[i][dataAction[i].length - 2];
            Music_Number = Music_Chosen;
            if (dataAction[i][4] != 0) {
              if (last_loop_byte == 0) {
                Is_Loop_Start = true;
              } else {
                Is_Loop_Start = false;
              }
              if (dataAction[i][4] < 128) {
                Is_Loop_End = true;
                Loop_Time = dataAction[i][4];
              } else {
                Is_Loop_End = false;
                Loop_Time = dataAction[i][4] - 128;
              }
            }
            if (dataAction[i][4] > 128) {
              last_loop_byte = dataAction[i][4];
            } else {
              last_loop_byte = 0;
            }

            var posDataDhInit = {
              number: number,
              Name: 1,
              Pos: Pos,
              Speed: Speed,
              Delay: Delay,
              Loop_Time: Loop_Time,
              Is_Loop_Start: Is_Loop_Start,
              Is_Loop_End: Is_Loop_End,
              Music_State: Music_State,
              Music_Number: Music_Number,
              Is_Cur_Form: Is_Cur_Form,
            };

            posDataArr.push(CreatePosDataDh(posDataDhInit));
          }
          //console.log(posDataArr);
          len = posDataArr.length;
          let currentNumber = 0;
          var Loop_Time = -1;
          var Is_In_Loop = false;
          var currentData = [[]];
          var flagNumber = posDataArr[0].number;
          currentData[0] = [];
          for (let i = 0; i < len; i++) {
            if (flagNumber != posDataArr[i].number) {
              flagNumber = posDataArr[i].number;
              currentNumber++;
              currentData[currentNumber] = [];
            }
            var currentPos = [];
            currentPos.push(0xff);
            currentPos.push(0x3e);
            currentPos.push(0x02);
            if (!Is_In_Loop) {
              currentPos[3] = 0;
            }
            if (
              Is_In_Loop &&
              !posDataArr[i].Is_Loop_Start &&
              !posDataArr[i].Is_Loop_End
            ) {
              currentPos[3] = Loop_Time + 128;
            }
            if (posDataArr[i].Is_Loop_End) {
              Is_In_Loop = false;
              currentPos[3] = Loop_Time;
              Loop_Time = 0;
            }
            if (posDataArr[i].Is_Loop_Start) {
              Is_In_Loop = true;
              Loop_Time = posDataArr[i].Loop_Time;
              currentPos[3] = Loop_Time + 128;
            }

            var _int_temp1 = 0;
            var _int_temp2 = 0;
            var _int_temp3 = 0;
            var _int_temp4 = 0;
            var _int_temp5 = 0;
            var _int_temp6 = 0;
            for (let k = 0; k < 48; k++) {
              if (k < 8) {
                if (posDataArr[i].Pos[k] < 0) {
                  _int_temp1 += parseInt(Math.pow(2, 7 - k));
                  posDataArr[i].Pos[k] = posDataArr[i].Pos[k] * -1;
                }
              }

              if (k >= 8 && k < 16) {
                if (posDataArr[i].Pos[k] < 0) {
                  _int_temp2 += parseInt(Math.pow(2, 15 - k));
                  posDataArr[i].Pos[k] = posDataArr[i].Pos[k] * -1;
                }
              }

              if (k >= 16 && k < 24) {
                if (posDataArr[i].Pos[k] < 0) {
                  _int_temp3 += parseInt(Math.pow(2, 23 - k));
                  posDataArr[i].Pos[k] = posDataArr[i].Pos[k] * -1;
                }
              }

              if (k >= 24 && k < 32) {
                if (posDataArr[i].Pos[k] < 0) {
                  _int_temp4 += parseInt(Math.pow(2, 31 - k));
                  posDataArr[i].Pos[k] = posDataArr[i].Pos[k] * -1;
                }
              }
              if (k >= 32 && k < 40) {
                if (posDataArr[i].Pos[k] < 0) {
                  _int_temp5 += parseInt(Math.pow(2, 39 - k));
                  posDataArr[i].Pos[k] = posDataArr[i].Pos[k] * -1;
                }
              }

              if (k >= 40 && k < 48) {
                if (posDataArr[i].Pos[k] < 0) {
                  _int_temp6 += parseInt(Math.pow(2, 47 - k));
                  posDataArr[i].Pos[k] = posDataArr[i].Pos[k] * -1;
                }
              }
            }

            currentPos[4] = _int_temp1;
            currentPos[5] = _int_temp2;
            currentPos[6] = _int_temp3;
            currentPos[7] = _int_temp4;
            currentPos[8] = _int_temp5;
            currentPos[9] = _int_temp6;
            for (let k = 0; k < 48; k++) {
              currentPos[k + 10] = posDataArr[i].Pos[k];
            }
            currentPos[58] = posDataArr[i].Speed;
            currentPos[59] = posDataArr[i].Delay;
            switch (posDataArr[i].Music_State) {
              case 1:
                currentPos[60] = parseInt(posDataArr[i].Music_Number / 256);
                currentPos[61] = parseInt(posDataArr[i].Music_Number % 256);
                break;
              case 2:
                currentPos[60] = parseInt(posDataArr[i].Music_Number / 256);
                currentPos[61] = parseInt(posDataArr[i].Music_Number % 256);
                break;
              case 3:
                currentPos[60] = 252;
                currentPos[61] = 252;
                break;
              case 4:
                currentPos[60] = 251;
                currentPos[61] = 251;
                break;
            }
            currentPos[62] = posDataArr[i].Is_Cur_Form;
            var sum = 0;
            var currentLen = currentPos.length;
            //console.log(currentLen);
            for (let k = 1; k < currentLen; k++) {
              sum += currentPos[k];
            }
            sum = sum % 256;
            currentPos[63] = sum;
            currentData[currentNumber].push(currentPos);
          }
          console.log(currentData);
          len = nameArr.length;
          for (let i = 0; i < len; i++) {
            let promise = new Promise((resolve, reject) => {
              setTimeout(() => resolve(), 50);
            });
            await promise;
            var selectedName = nameArr[i];
            var typeName = selectedName.substring(0, 3);
            var actionName = selectedName.substring(3);
            if (typeName == "人型:") {
              typeName = "RobotAction/";
            } else if (typeName == "龙型:") {
              typeName = "Action/";
            }
            actionName = typeName + actionName + ".sh";
            //console.log(actionName);
            var nameByte = Array.from(iconv.encode(actionName, "utf8"));
            var length = nameByte.length;
            var senlength = length + 2;
            //删除
            var createCMD = [];

            createCMD[0] = 0xff;
            createCMD[1] = senlength;
            createCMD[2] = 0x15;
            var sum = senlength + 0x15;
            for (let j = 0; j < length; j++) {
              createCMD.push(nameByte[j]);
              sum = sum + nameByte[j];
            }
            sum = sum % 256;
            createCMD.push(sum);
            var dataUt = new Uint8Array(createCMD);
            await this.hidDevice.sendReport(0, dataUt);
            // console.log(dataUt);
            //这里需要延迟，钢索的是有返回消息的，但是根据流程，可以不处理
            let promise1 = new Promise((resolve, reject) => {
              setTimeout(() => resolve(), 50);
            });
            await promise1;

            //创建
            createCMD[2] = 0x14; //创建指令
            createCMD[createCMD.length - 1] =
              createCMD[createCMD.length - 1] - 1;
            dataUt = new Uint8Array(createCMD);
            await this.hidDevice.sendReport(0, dataUt);
            let promise2 = new Promise((resolve, reject) => {
              setTimeout(() => resolve(), 50);
            });
            await promise2;
            //发送数据
            for (let j = 0; j < currentData[i].length; j++) {
              let promises = new Promise((resolve, reject) => {
                setTimeout(() => resolve(), 50);
              });
              await promises;
              var dataSen = new Uint8Array(currentData[i][j]);

              console.log(dataSen);
              await this.hidDevice.sendReport(0, dataSen);
            }
          }
        }
      };
    },
    async timeSendingPosition() {
      //时时发送位置
      try {
        if (this.isLink == true) {
          var data = [];
          for (let i = 0; i < this.initData[0].length; i++) {
            data.push(this.initData[0][i]);
          }
          // console.log(data);
          data[0] += parseInt(this.ArmInput);
          data.push(0x23);
          var sendData = [];
          sendData[0] = 0xff;
          //sendData[1] = 0xff;
          sendData[1] = 0x33;
          sendData[2] = 0x01;
          var sum = 0x33 + 0x01;
          for (let i = 0; i < data.length; i++) {
            sendData[3 + i] = data[i];
            sum += data[i];
          }
          sum = sum % 256;
          sendData[sendData.length] = sum;
          var dataUt = new Uint8Array(sendData);
          console.log(dataUt);
          await this.hidDevice.sendReport(0, dataUt);
        }
      } catch (err) {
        console.error(err);
      }
    },
    async getActionName() {
      //获取动作文件名
      try {
        if (this.isLink == true) {
          var sendByte = [];
          sendByte[0] = 0xff;
          sendByte[1] = 0x02;
          sendByte[2] = 0x0e;
          sendByte[3] = 0x10;
          this.isRedActionLoong = true;
          await this.hidDevice.sendReport(0, sendByte);
        }
      } catch (err) {
        console.error(err);
      }
    },
    async receivingProcess(
      event //接收消息线程池
    ) {
      try {
        const { data } = event;
        const dataU8 = new Uint8Array(data.buffer);
        console.log(dataU8);
        var len = dataU8.length;
        for (let i = 0; i < len; i++) {
          if (dataU8[i] == 0xff && dataU8[i + 1] == 0xff) {
            //获取指令，长度，计算sum和
            var dataLen = dataU8[i + 2]; //长度
            var order = dataU8[i + 3]; //指令
            if (dataLen == 0x32 && order == 0xf5) {
              // 连接pc编译器
              var dataAction = [];
              for (let j = 0; j < 48; j++) {
                dataAction[j] = dataU8[i + j + 4];
              }
              this.initData.push(dataAction);
            } else if (
              dataLen == 0x04 &&
              order == 0xf5 &&
              dataU8[i + 4] == 0x4f &&
              dataU8[i + 5] == 0x4b
            ) {
              //连接pc编译器发送成功后的处理
              this.isLink = true;
              this.isInitData = true;
              console.log("isInitData = " + this.isInitData);
            } else if (
              dataLen == 0x04 &&
              order == 0xf6 &&
              dataU8[i + 4] == 0x4f &&
              dataU8[i + 5] == 0x4b
            ) {
              //断开机器人
              this.isLink = false;
              this.hidDevice.close();
            } else if (order == 0x0e) {
              //需要计算根据数据长度读和sum和进行校验
              var dataSum = order + dataLen;
              var nameContent = [];
              for (let j = 0; len < dataLen - 1; j++) {
                dataSum += dataU8[4 + j];
                nameContent.push(dataU8[4 + j]);
              }
              if (dataSum % 256 == dataU8[4 + dataSum]) {
                actionList.push("龙型:" + iconv.decode(nameContent, "gb2312"));
              }
            } else if (
              dataLen == 0x04 &&
              order == 0xfa &&
              dataU8[i + 4] == 0x4f &&
              dataU8[i + 5] == 0x4b &&
              dataU8[i + 6] == 0x98 &&
              this.isRedActionLoong == true
            ) {
              //说明第一个文件读取结束了
              this.isRedActionLoong == false;
              var namebyte = conv.encode("RobotAction", "gb2312");
              //开始发送第二个指令
              var sendByte = [];
              sendByte[0] = 0xff;
              sendByte[1] = 0x02;
              sendByte[2] = 0x0e;
              sendByte[3] = 0x10;
              this.isRedActionLoong = true;
              await this.hidDevice.sendReport(0, sendByte);
            }
          }
        }
      } catch (err) {
        console.log(err);
      }
    },
  },
};
</script>
