  methods: {
    async connectToDevice() {
      try {
        const options = {
          //acceptAllDevices: true,
          filters: [
            {
              namePrefix: "BUZZ",
            },
          ],
          optionalServices: [0xffc0],
        };
        await navigator.bluetooth.requestDevice(options).then(async (dev) => {
          this.device = dev;
          this.server = await this.device.gatt.connect();
          await new Promise((resolve) => setTimeout(resolve, 1000));
          if (this.server.connected) {
            console.log(1);
            const services = await this.server.getPrimaryServices(0xffc0);
            this.characteristic = await services[0].getCharacteristic(0xffc1);
            this.characteristic.startNotifications();
            this.characteristic.addEventListener(
              "characteristicvaluechanged",
              this.ReadDataMessage
            );
            this.isLink = true;
          }
        });
      } catch (err) {
        if (
          err instanceof DOMException &&
          err.code === DOMException.NOT_FOUND_ERR
        ) {
          this.isLink = false;
          console.log("连接设备出错:", err);
        } else {
          this.isLink = false;
          console.error(err);
        }
      }
    }, //连接

    async ReadDataMessage(data) {
      const dataArr = new Uint8Array(data.target.value.buffer);
      console.log(dataArr);
      var len = dataArr.length;
      for (let i = 0; i < len; i++) {
        if (dataArr[i] == 0x55 && dataArr[i + 1] == 0xaa) {
          var high = dataArr[i + 2];
          var low = dataArr[i + 3];
          var getDatalen = (high << 8) | low;
          var main_Cmd = dataArr[i + 4];
          var sub_Cmd = dataArr[i + 5];
          if (main_Cmd == 0x13 && sub_Cmd == 0x16) {
            var crcHigh = dataArr[i + 3 + getDatalen - 2];
            var crcLow = dataArr[i + 3 + getDatalen - 1];
            var crcValue = (crcHigh << 8) | crcLow;
            var crc = 0xffff;
            for (let j = 0; j < getDatalen - 1; j++) {
              crc =
                (crc >> 8) ^
                this.Crc16_Table[(crc ^ dataArr[i + 2 + j]) & 0xff];
            }
            if (crc == crcValue) {
              if (this.upFileNameFlag == 0) {
              }
            } else {
              console.log("验证失败");
            }
          }
        }
      }
    }, //接收处理池,

    async SendDataFormatMessage(data) {
      var sendData = [];
      var crc = 0xffff;
      var crcData = [];
      sendData.push(0x55);
      sendData.push(0xaa);
      var len = data.length + 3;
      sendData.push((len >> 8) & 0xff);
      crcData.push((len >> 8) & 0xff);
      sendData.push(len & 0xff);
      crcData.push(len & 0xff);
      for (let i = 0; i < data.length; i++) {
        sendData.push(data[i]);
        crcData.push(data[i]);
      }
      for (let byte of crcData) {
        crc = (crc >> 8) ^ this.Crc16_Table[(crc ^ byte) & 0xff];
      }
      sendData.push((crc >> 8) & 0xff); //高位
      sendData.push(crc & 0xff); //低位
      var sum = 0;
      for (let i = 0; i < sendData.length; i++) {
        sum += sendData[i];
      }
      sendData.push(sum & 0xff);
      console.log(sendData);
      await this.characteristic.writeValue(new Uint8Array(sendData));
    }, //发送处理方法

    async UploadActionFileNames() {
      if (this.isLink && this.isUpFileName == false) {
        this.isUpFileName = true;
        this.upFileNameFlag = 0;
        //有两个文件凌空地面
        var data = [];
        data.push(0x13);
        data.push(0x16);
        var actionName = Array.from(iconv.encode("Action/rack", "GB18030")); //地面
        for (let i = 0; i < actionName.length; i++) {
          data.push(actionName[i]);
        }
        console.log(data);
        this.SendDataFormatMessage(data);
